<template>
  <el-input-number
    v-model="iModelValue[name]"
    :min="min"
    :max="max"
    :size="size"
    :name="name"
    :label="label"
    :disabled="disabled"
    :precision="precision"
    :placeholder="placeholder"
    :controls-position="controlsPosition"
  />
</template>

<script>
import { defineComponent, reactive, watch } from 'vue';
import { ElInputNumber } from 'element-plus';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import mixinProps from '../../props/form/number';
import initData from '../../props/init-data';

export default defineComponent({
  name: 'MisNumber',
  components: {
    ElInputNumber,
  },
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = Number(val);
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = Number(val);
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    return {
      ...useDerivedProps(props),
      iModelValue,
      state,
    };
  }
});
</script>
