<template>
  <el-menu
    ref="menuRef"
    :mode="mode"
    :default-active="defaultActive"
    :collapse="state.data.collapse"
    :class="state.data.collapse ? `${classname} umis-menu--collapse` : classname"
    :router="router"
    :background-color="backgroundColor"
    :text-color="textColor"
    :active-text-color="activeTextColor"
    :unique-opened="uniqueOpened"
    @select="handleSelect"
  >
    <template v-if="title">
      <mis-component
        :props="title"
        :mis-name="title.renderer"
        :path="`${path}/${title.renderer}`"
      />
    </template>
    <template
      v-for="(item, index) in menuList"
      :key="index"
    >
      <mis-component
        :path="`${path}/${index}/${item.renderer}`"
        :mis-name="item.renderer"
        :name="item.name"
        :body="item.body"
        :props="item"
        :index="index"
      />
    </template>
  </el-menu>
</template>

<script>
import {
  defineComponent, onMounted, nextTick, watch, ref,
} from 'vue';
import { useRouter } from 'vue-router';
import { ElMenu } from 'element-plus';
import useInitApi from '../../../hooks/useInitApi';
import useInitData from '../../../hooks/useInitData';
import useLinkage from '../../../hooks/useLinkage';
import initData from '../../props/init-data';
import initApi from '../../props/init-api';
import mixinProps from '../../props/menu';

// import menuCreator from '../../../router/creator';

export default defineComponent({
  name: 'MisMenu',
  components: {
    ElMenu,
  },
  mixins: [mixinProps, initApi, initData],
  setup(props) {
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const { onLinkageTrigger } = useLinkage(props);
    const router = useRouter();
    const width = ref(0);
    const menuList = ref();
    const activeIndex = ref('');
    const menuRef = ref(null);
    let defaultWidth;
    const autoWidth = () => {
      nextTick(() => {
        width.value = menuRef.value.$el.clientWidth;
        state.data.defaultWidth = menuRef.value.$el.clientWidth;
        if (props.target) {
          onLinkageTrigger(props.target, { width: width.value, activeIndex: activeIndex.value });
        }
      });
    };

    state.data.collapse = false;

    watch(() => props.defaultActive, (val) => {
      activeIndex.value = val;
    }, {
      immediate: true,
    });
    watch(() => state.data.collapse, (val) => {
      if (val) {
        onLinkageTrigger(props.target, { width: 64 });
      } else {
        onLinkageTrigger(props.target, { width: defaultWidth });
      }
      state.data.collapse = val;
    });

    const handleSelect = (index) => {
      activeIndex.value = index;
      autoWidth();
    };

    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      } else {
        menuList.value = props.body;
      }
    };

    const fetchDataList = () => {
      handleFetchApi(props.initApi, state.data)
        .then((result) => {
          const dyRouter = []; // menuCreator.dynamicMenuCreator(result.menu).menus;
          menuList.value = result.menu;
          dyRouter.forEach(item => {
            router.addRoute('UmisWebsite', item);
          });
          nextTick(() => {
            if (props.defaultActive && props.router) {
              router.push(props.defaultActive);
            }
          });
        });
    };

    onMounted(() => {
      defaultWidth = props.initData.width;
      onComponentUpdate();
    });

    return {
      ...useLinkage(props),
      onComponentUpdate,
      shouldComponentUpdate,
      handleSelect,
      state,
      width,
      menuList,
      activeIndex,
      menuRef,
    };
  },
});
</script>
