import { onMounted, onBeforeUnmount, getCurrentInstance } from 'vue';
import clonedeep from 'lodash.clonedeep';
export default function (props) {
  const { proxy } = getCurrentInstance();
  const onLinkageTrigger = (target, context) => {
    if (target) {
      proxy.$eventHub.$emit('mis-component:linkage', target, context);
    }
  };

  const getURLParameters = (url) => url.match(/([^?=&]+)(=([^&]*))/g).reduce((total, item) => {
    const [key, value] = item.split('=');
    total[key] = value.replace('${', '').replace('}', '');
    return total;
  }, {});

  const handleLinkage = (linkage, context) => {
    if (linkage) {
      const [target, url] = linkage.split('?');
      let data = {};
      let isUpdate = false;
      let isLinked = true;

      if (props && props.name && target.includes(props.name)) {
        if (props.linkedOn) {
          isLinked = proxy.$expressionEval(props.linkedOn, {data: proxy.state.data, target: context});
        }
        if (isLinked) {
          if (url) {
            const fields = getURLParameters(url);
            for (const field in fields) {
              isUpdate = true;
              if (Object.prototype.hasOwnProperty.call(fields, field)) {
                if (field === '*') {
                  data = context;
                } else if (fields[field] === '*') {
                  data[field] = context;
                } else {
                  data[field] = context[fields][field];
                }
              }
            }
          } else {
            isUpdate = true;
            data = { ...proxy.state.data || {}, ...context };
          }
        }
      }
      if (isUpdate && isLinked) {
        proxy.state.data = clonedeep(data);
      }
    }
  };

  onMounted(() => {
    if (props && props.name) {
      proxy.$eventHub.$on('mis-component:linkage', handleLinkage, props.path);
    }
  });

  onBeforeUnmount(() => {
    if (props && props.name) {
      proxy.$eventHub.$off('mis-component:linkage', handleLinkage, props.path);
    }
  });

  return {
    onLinkageTrigger,
    handleLinkage,
  };
}
