<template>
  <div
    class="umis-page__container"
    :class="classname"
  >
    <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
      <template
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
      >
        <mis-component
          v-bind="iAttrs(child)"
          :mis-name="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :props="iAttrs(child)"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <mis-component
      v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
      v-bind="iAttrs(body)"
      :mis-name="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
    <div
      v-if="iProtal"
      class="umis-component__fragment"
    >
      <template
        v-for="({ actionType, body, data, visible }, track) in popMap"
        :key="track"
      >
        <component
          v-bind="body"
          :is="actionType"
          :path="track"
          :visible="visible"
          :init-data="data"
          :on-popup-invisible="destroyProtal"
          destroy-on-close
          lock-scroll
        />
      </template>
    </div>
    <section>
      <div
        v-for="({actionType, body}, path) in displayMap"
        :key="path"
        :track-id="actionType"
        :track="path"
        class="umis-page-container__pop"
      >
        <mis-wrapper
          classname="umis-toolmaker__pops__wrapper"
          :header="body.header"
          :body="body.body"
          :footer="body.footer"
          :path="path"
        />
      </div>
    </section>
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onBeforeMount, onBeforeUnmount, onMounted, reactive, ref} from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/page';
import {getUrlParams} from '../../utils/url';

export default defineComponent({
  name: 'MisPage',
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const popMap = reactive({});
    const displayMap = ref({});
    const createProtal = (path, pop) => popMap[path] = pop;
    const destroyProtal = (path) => { delete popMap[path]; };
    const showProtal = (actionType, path, body) => {
      if (!displayMap.value[path]) {
        displayMap.value[path] = {
          actionType,
          body
        };
      }
    };
    const hiddenProtal = (actionType, path, body) => {
      if (path) {
        delete displayMap.value[path];
      } else {
        displayMap.value = {};
      }
    };

    onBeforeMount(() => {
      if (props.mergeUrlQuery) {
        const defaultQuery = getUrlParams();
        Object.assign(state.data, defaultQuery);
      }
    });
    onMounted(() => {
      if (props.iProtal) {
        proxy.$eventHub.$on('mis-portal:create', createProtal);
        proxy.$eventHub.$on('mis-portal:destroy', destroyProtal);
      }

      proxy.$eventHub.$on('mis-portal:show', showProtal);
      proxy.$eventHub.$on('mis-portal:hidden', hiddenProtal);
    });
    onBeforeUnmount(() => {
      if (props.iProtal) {
        proxy.$eventHub.$off('mis-portal:create', createProtal);
        proxy.$eventHub.$off('mis-portal:destroy', destroyProtal);
      }

      proxy.$eventHub.$off('mis-portal:show', showProtal);
      proxy.$eventHub.$off('mis-portal:hidden', hiddenProtal);
    });
    return {
      ...useDerivedProps(),
      destroyProtal,
      displayMap,
      popMap,
      state
    };
  },
});
</script>
