<template>
  <el-descriptions
    :title="title"
    :border="border"
    :direction="direction"
    :size="size"
    :column="column"
  >
    <template
      v-for="(control, index) in controls"
      :key="index"
    >
      <el-descriptions-item
        :label="control.label"
        :width="width"
        :label-align="control.labelAlign"
        :align="control.align"
        :class-name="control.classname"
        :label-class-name="control.labelClassName"
      >
        <mis-component
          v-if="Object.prototype.toString.call(control.body) === '[object Object]'"
          v-bind="iAttrs(control.body)"
          :mis-name="control.body.renderer"
          :path="`${path}/body/${control.body.renderer}`"
          :props="iAttrs(control.body)"
          :header="iHeader(control.body)"
          :body="iBody(control.body)"
          :footer="iFooter(control.body)"
          :init-data="state.data"
        />
        <template v-else>
          <span class="umis-form__static__text">
           {{ state.data[control.name] }}
          </span>
        </template>
      </el-descriptions-item>
    </template>
  </el-descriptions>
</template>

<script>
import { defineComponent } from 'vue';
import { ElDescriptions, ElDescriptionsItem } from 'element-plus';

import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import mixinProps from '../../props/static';
import initData from '../../props/init-data';

export default defineComponent({
  name: 'MisStatic',
  components: {
    ElDescriptions,
    ElDescriptionsItem,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
