import axios from 'axios';
import { config } from './config';

const { isCancel } = axios;
const CancelToken = axios.CancelToken;
let reqAdaptor;
let resAdaptor;
let umisApi = null;

function successInterceptor(response) {
  if (!Object.hasOwnProperty.call(response.data, 'status')) {
    response.data.status = response.data.code;
  }
  if (
    response
    && response.data
    && [100000, 10000, 1000, 200, 0].includes(response.data.status)
  ) {
    const {
      data,
      config: { url, method, params },
    } = response;
    if (!resAdaptor && config.adaptor.res) {
      /* eslint-disable */
      resAdaptor = new Function('url', 'method', 'params', 'res', config.adaptor.res);
    }
    if (resAdaptor) {
      try {
        resAdaptor(url, method, params, data);
      } catch (e) {
        console.error('响应适配器报错了！', e);
      }
    }
    return data;
  }
  return Promise.reject(response);
}

function errorInterceptor(error) {
  if (axios.isCancel(error) && error.message === 'CANCELED_DUE_TO_NEW_REQUEST') {
    return Promise.reject(error);
  }
  return Promise.reject(error);
}

function requestInterceptor(req) {
  if (!req.params) {
    req.params = {};
  }
  if (!reqAdaptor && config.adaptor.req) {
    /* eslint-disable */
    reqAdaptor = new Function('url', 'method', 'params', 'req', config.adaptor.req);
  }
  if (reqAdaptor) {
    try {
      reqAdaptor(req.url, req.method, req.params, req);
    } catch (e) {
      console.error('请求适配器报错了！', e);
    }
  }
  return req;
}

function factory(baseUrl, configs) {
  const mergedConfigs = { ...configs || {} };
  if (baseUrl) {
    mergedConfigs.baseURL = baseUrl;
  }
  if (!mergedConfigs.params) {
    mergedConfigs.params = {};
  }
  const instance = axios.create(mergedConfigs);

  instance.interceptors.request.use(
    (req) => {
      return requestInterceptor(req);
    },
    (error) => Promise.reject(error),
  );
  instance.interceptors.response.use(
    (response) => successInterceptor(response),
    (error) => errorInterceptor(error),
  );
  return instance;
}

function apiFactory() {
  return {
    isCancel,
    CancelToken,
    factory,
    staticApi() {
      return factory(config.activeDomain);
    },
    slientApi() {
      if (!umisApi || config.isApiChanged) {
        umisApi = factory(config.activeDomain);
      }
      return umisApi;
    },
  };
}

export default apiFactory;
