<template>
  <el-card
    v-if="iVisible"
    v-show="iHidden"
    :shadow="shadow"
    :body-style="bodyStyle"
    :class="`umis-card ${classname}`"
    @click="handleClick"
  >
    <template
      v-if="header && header.length"
      #header
    >
      <template
        v-if="Object.prototype.toString.call(header) === '[object Array]'"
      >
        <template
          v-for="(child, index) in header"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            :mis-name="child.renderer"
            :path="`${path}/header/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(header) === '[object Object]'"
        :mis-name="header.renderer"
        :path="`${path}/header/${header.renderer}`"
        :body="iBody(header)"
        :header="iHeader(header)"
        :footer="iFooter(header)"
        :props="iAttrs(header)"
        :init-data="state.data"
      />
      <template v-else-if="Object.prototype.toString.call(header) === '[object String]'">
        {{ header }}
      </template>
    </template>
    <div
      v-if="body"
      class="umis-card__body"
    >
      <template
        v-if="Object.prototype.toString.call(body) === '[object Array]'"
      >
        <template
          v-for="(child, index) in body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            :mis-name="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
        :mis-name="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :props="iAttrs(body)"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="state.data"
      />
      <template v-else-if="Object.prototype.toString.call(header) === '[object String]'">
        {{ body }}
      </template>
    </div>
    <div
      v-if="footer"
      class="umis-card__footer"
    >
      <template
        v-if="Object.prototype.toString.call(footer) === '[object Array]'"
      >
        <template
          v-for="(child, index) in footer"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            :mis-name="child.renderer"
            :path="`${path}/footer/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(footer) === '[object Object]'"
        :mis-name="footer.renderer"
        :path="`${path}/footer/${footer.renderer}`"
        :header="iHeader(footer)"
        :body="iBody(footer)"
        :footer="iFooter(footer)"
        :props="iAttrs(footer)"
        :init-data="state.data"
      />
    </div>
  </el-card>
</template>

<script>
import { defineComponent, getCurrentInstance } from 'vue';
import { ElCard } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useLinkage from '../../hooks/useLinkage';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/card';

export default defineComponent({
  name: 'MisCard',
  components: {
    ElCard,
  },
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const renderHeader = () => proxy.$getRenderedTpl(props.header, state.data);

    const handleClick = () => {
      if (props.actions && props.actions.length) {
        if (Object.prototype.toString.call(props.actions) === '[object Array]') {
          const props = props.actions.find((item) => {
            if (
              item.actionOn
              && proxy.$expressionEval(item.actionOn, state.data)
            ) {
              return item;
            }
          });
          props.actions && props.action(props, state.data);
        } else {
          props.actions && props.action(props.actions, state.data);
        }
      }
    };

    return {
      ...useDerivedProps(props),
      ...useLinkage(props),
      ...useVisible(props),
      renderHeader,
      handleClick,
      state,
    };
  },
});
</script>
