import { onBeforeMount, reactive } from 'vue';
import { getUrlParams } from '../utils/url';

export default function (props) {
  const pageInfo = {
    pageIndex: 1,
    pageSize: 10,
    total: 0,
    hasMore: true,
  };

  onBeforeMount(() => {
    if (props.hasPageInfo) {
      const defaultQuery = getUrlParams();
      const { pageIndex, pageSize } = defaultQuery;
      if (pageIndex) {
        pageInfo.pageIndex = Number(pageIndex);
      }
      if (pageSize) {
        pageInfo.pageSize = Number(pageSize);
      }
    }
  });

  return pageInfo;
}
