<template>
  <component
    v-loading="loading"
    v-bind="iAttrs(props)"
    :is="componentName"
    ref="component"
    :key="updatedCount"
    :track-id="componentName"
    :index="index"
    :path="path"
    :track="path"
    :class="className"
    :header="iHeader()"
    :body="iBody()"
    :footer="iFooter()"
    :init-data="iData(props)"
    :error-info="errorInfo"
    :action="filterAction"
    :after-action="afterAction"
  />
</template>
<script>
import {defineComponent, computed, ref, onErrorCaptured, onBeforeMount, onMounted, onBeforeUnmount, getCurrentInstance} from 'vue';
import {useRouter} from 'vue-router';

import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initApi from '../props/init-api';
import initData from '../props/init-data';
import mixinProps from '../props/component';

export default defineComponent({
  name: 'MisComponent',
  mixins: [mixinProps, initData, initApi],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const router = useRouter();
    const { handleFetchApi } = useInitApi(props);
    const state = useInitData(props);
    const updatedCount = ref(0);
    const loading = ref(false);
    const componentName = computed(() => (error.value ? 'mis-error' : props.misName));

    const error = ref('');
    const errorInfo = computed(() => {
      if (error.value) {
        return {
          props,
          error: error.value,
        };
      }
      return {};
    });

    const filterAction = (config, context, feedback) => {
      if (
        (config.renderer === 'mis-action' && config.actions)
        || (config.renderer === 'mis-chart' && config.footer.actionType)
      ) {
        if (config && ['mis-submit', 'mis-reset'].includes(config.actionType)) {
          return props.action();
        }
        return dispatchAction(config, context, feedback);
      } if (['mis-submit', 'mis-reset'].includes(config.actionType)) {
        return props.action();
      }
      return dispatchAction(config, context, feedback);
    };

    const dispatchAction = (config, context, feedback) => {
      switch (config.actionType) {
      case 'mis-linkage':
        handleLinkageAction(config, context, feedback);
        break;
      case 'mis-ajax':
        handleAjaxAction(config, context, feedback);
        break;
      case 'mis-url':
        handleUrlAction(config, context);
        break;
      case 'mis-copy':
        handleCopyAction(config, context);
        break;
      case 'mis-dialog':
        handleShowPopup(config, context);
        break;
      case 'mis-drawer':
        handleShowPopup(config, context);
        break;
      case 'mis-fullscreen':
        handleFullScreen(config, context);
        break;
      }
    };

    const onReloadAction = (config, res) => {
      const { reload } = config;
      if (reload) {
        proxy.$eventHub.$emit('mis-component:reload', reload, res);
      }
    };

    const onComponentReload = (target, res) => {
      if (target === 'window') {
        window.location.reload();
      } else if (props && target === props.name) {
        let rowIndex = -1;
        try {
          if (res && ['mis-table', 'mis-cards'].includes(props.misName) && props.props.updateOnRowId) {
            const rows = proxy.$.refs.component.rows;
            for (let index = 0; index < rows.length; index++) {
              if (String(rows[index][props.props.updateOnRowId]) === String(res[props.props.updateOnRowId])) {
                rowIndex = index;
                break;
              }
            }
          }
        } catch (e) {
          console.warn(e);
        }
        if (rowIndex !== -1) {
          proxy.$.refs.component.rows.splice(rowIndex, 1, res);
        } else {
          updatedCount.value++;
        }
      }
    };

    const handleLinkageAction = (config, context, feedback) => {
      feedback();
    };

    const handleAjaxAction = (config, context, feedback) => {
      loading.value = true;
      handleFetchApi(config.actionApi, context)
        .then(res => {
          feedback && feedback(res);
          onReloadAction(config, res);
        })
        .finally(() => {
          loading.value = false;
        });
    };

    const handleUrlAction = (config, context) => {
      const url = proxy.$compiledUrl(config.url, context);
      if (/^https?:\/\//.test(url)) {
        config.blank ? window.open(url) : (window.location.href = url);
      } else {
        const params = proxy.$compiledParams(config.params, context);
        router.push({
          path: url,
          query: params
        });
      }
    };

    const handleCopyAction = async(config, context) => {
      try {
        const content = proxy.$compiledUrl(config.content, context);
        const copy = await import('copy-to-clipboard');
        copy.default(content);
        proxy.$message.success('已复制到粘贴板');
      } catch (e) {
        proxy.$message.error(e.message);
        console.error(e);
      }
    };

    const handleShowPopup = (config, context) => {
      proxy.$eventHub.$emit('mis-portal:create', config.path, {
        body: config.body,
        data: context,
        actionType: config.actionType,
        visible: true,
      });
    };

    const handleFullScreen = (config, context) => {
      const ele = config.target || document.body;
      const isFull = !!(document.fullscreen || document.mozFullScreen || document.webkitIsFullScreen || document.webkitFullScreen || document.msFullScreen);

      if (isFull) {
        if (document.exitFullScreen) {
          document.exitFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) {
          document.webkitExitFullscreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        proxy.$message.success('再次点击将退出全屏');
        if (ele.requestFullscreen) {
          ele.requestFullscreen();
        } else if (ele.mozRequestFullScreen) {
          ele.mozRequestFullScreen();
        } else if (ele.webkitRequestFullscreen) {
          ele.webkitRequestFullscreen();
        } else if (ele.msRequestFullscreen) {
          ele.msRequestFullscreen();
        }
      }
    };

    onBeforeMount(() => {
      try {
        const misName = props.misName
          .split('-')
          .map((kebab) => kebab.charAt(0).toUpperCase() + kebab.slice(1))
          .join('');
        if (!proxy.$.appContext.components[misName]) {
          error.value = '找不到对应的渲染器';
        }
      } catch (e) {
        console.error(props);
      }
    });

    onMounted(() => {
      if (props && props.name) {
        proxy.$eventHub.$on('mis-component:reload', onComponentReload);
      }
    });

    onBeforeUnmount(() => {
      if (props && props.name) {
        proxy.$eventHub.$off('mis-component:reload', onComponentReload);
      }
    });

    onErrorCaptured((err, vm, info) => {
      error.value = `'${err.message}' is found in ${props.misName} renderer`;
      console.error(err, vm, info);
      return false;
    });

    return {
      ...useDerivedProps(props),
      filterAction,
      handleFetchApi,
      componentName,
      updatedCount,
      errorInfo,
      loading,
      state,
    };
  },
});
</script>
