<template>
  <div class="umis-form__list">
    <el-checkbox-group
      v-if="multiple"
      v-model="iModelValue[name]"
      :min="min"
      :max="max"
    >
      <template
        v-for="(item, index) in iOptions"
        :key="index"
      >
        <div
          class="umis-list__item"
          :class="{ checked: iModelValue[name].includes(item.value) }"
          @click="handleClick(index)"
        >
          <el-checkbox
            :ref="setRef"
            :label="item.value"
          />
          <mis-component
            :mis-name="body.renderer"
            :path="`${path}/${body.renderer}`"
            :index="index"
            :props="iAttrs(body)"
            :header="iHeader(body)"
            :body="iBody(body)"
            :footer="iFooter(body)"
            :init-data="item"
          />
        </div>
      </template>
    </el-checkbox-group>
    <el-radio-group
      v-else
      v-model="iModelValue[name]"
    >
      <template
        v-for="(item, index) in iOptions"
        :key="index"
      >
        <div
          class="umis-list__item"
          :class="{ checked: iModelValue[name].includes(item.value) }"
          @click="handleClick(index)"
        >
          <el-radio
            :ref="setRef"
            :label="item.value"
          />
          <mis-component
            :mis-name="body.renderer"
            :path="`${path}/${body.renderer}`"
            :index="index"
            :props="iAttrs(body)"
            :header="iHeader(body)"
            :body="iBody(body)"
            :footer="iFooter(body)"
            :init-data="item"
          />
        </div>
      </template>
    </el-radio-group>
  </div>
</template>
<script>
import { defineComponent, reactive, watch } from 'vue';
import {
  ElCheckboxGroup, ElCheckbox, ElRadioGroup, ElRadio,
} from 'element-plus';

import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/list';

export default defineComponent({
  name: 'MisList',
  components: {
    ElCheckboxGroup,
    ElCheckbox,
    ElRadioGroup,
    ElRadio,
  },
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});
    const iOptions = reactive(props.options);
    const setRef = (el) => optionRefs.push(el);
    const handleClick = (index) => {
      optionRefs[index].$el.click();
    };
    const optionRefs = [];

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });
    return {
      ...useDerivedProps(props),
      iOptions,
      iModelValue,
      state,
      setRef,
      handleClick,
    };
  },
});
</script>
