<template>
  <el-aside
    v-if="iVisible"
    v-show="iHidden"
    :class="classname"
    :width="`${state.data.width}px`"
    class="umis-aside__container"
  >
    <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
      <template
        v-for="(child, index) in body"
        :key="index"
      >
        <mis-component
          v-bind="iAttrs(child)"
          :mis-name="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :props="iAttrs(child)"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <mis-component
      v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
      v-bind="iAttrs(body)"
      :mis-name="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :props="iAttrs(body)"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :init-data="state.data"
    />
  </el-aside>
</template>

<script>
import { defineComponent } from 'vue';
import { ElAside } from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useLinkage from '../../hooks/useLinkage';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/aside';

export default defineComponent({
  name: 'MisAside',
  components: {
    ElAside,
  },
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const state = useInitData(props);

    return {
      ...useLinkage(props),
      ...useDerivedProps(props),
      ...useVisible(props),
      state,
    };
  },
});
</script>
