<template>
  <div class="umis-each__container">
    <template
      v-for="(item, index) in state.data"
      :key="`${path}/${index}/${body.renderer}`"
    >
      <mis-component
        v-bind="iAttrs(body)"
        :mis-name="body.renderer"
        :path="`${path}/${index}/${body.renderer}`"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :init-data="item"
      />
    </template>
  </div>
</template>

<script>
import { defineComponent } from 'vue';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/each';

export default defineComponent({
  name: 'MisEach',
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);

    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
