import { ref, getCurrentInstance, onMounted } from 'vue';

export default function () {
  const { proxy } = getCurrentInstance();
  const schemaInstance = ref();

  const beforeJSONEdit = () => {
    let trackList = proxy.state.activeTrack.split('/');
    trackList = trackList.slice(2);
    return findOneInJSON(schemaInstance.value, trackList);
  };

  const findMisComponent = (node) => {
    while (node && !node.classList.contains('umis-toolmaker__container') && !node.getAttribute('track')) {
      node = node.parentNode;
    }
    return node;
  };

  const swapOneInJSON = (json, trackIndex, targetIndex) => {
    json[trackIndex] = json.splice(targetIndex, 1, json[trackIndex])[0];
  };

  const findOneInJSON = (json, tracks, pJson, trackIndex) => {
    const track = tracks.shift();

    let index = trackIndex;
    if (tracks.length === 1) {
      index = track;
    }
    if (!track) {
      return { json, pJson, trackIndex: Number(trackIndex) };
    }
    if (track.startsWith('mis-') && json.renderer === track) {
      return findOneInJSON(json, tracks, pJson, index);
    }
    if (json[track]) {
      return findOneInJSON(json[track], tracks, json, index);
    }
  };

  onMounted(() => {
    const UMIS = window.UMIS || {};
    schemaInstance.value = UMIS.pageSchema;
  });

  return {
    findMisComponent,
    beforeJSONEdit,
    swapOneInJSON,
    schemaInstance
  };
}
