<template>
  <div
    v-loading="loading"
    class="umis-qrcode__container"
    :class="classname"
  >
    <div ref="qrcode" />
  </div>
</template>
<script>
import {defineComponent, onMounted, ref, getCurrentInstance} from 'vue';
import mixinProps from '../props/qrcode';

export default defineComponent({
  name: 'MisQrcode',
  mixins: [mixinProps],
  setup(props) {
    const {proxy} = getCurrentInstance();
    const loading = ref(false);
    const init = async() => {
      try {
        const QRCode = await import('qrcode2');
        new QRCode.default(proxy.$.refs.qrcode, {
          width: props.size,
          height: props.size,
          text: props.value,
          colorDark: props.colorDark,
          colorLight: props.colorLight,
        });
      } catch (e) {
        proxy.$message.error(e.message);
        console.error(e);
      } finally {
        loading.value = false;
      }
    };

    onMounted(() => {
      loading.value = true;
      init();
    });

    return {
      loading
    };
  },
});
</script>
