import clonedeep from 'lodash.clonedeep';
import { getCurrentInstance, reactive, watch } from 'vue';

export default function (props) {
  const { proxy } = getCurrentInstance();
  const state = reactive({
    data: {},
  });

  watch(() => props.initData, (val, old) => {
    let mergeData = {};
    if (props.inherit.type !== 'none') {
      if (props.inherit.type === 'all') {
        mergeData = { ...state.data, ...val };
      } else if (props.inherit.type === 'include') {
        props.inherit.value.forEach((key) => {
          if (Object.hasOwnProperty.call(val, key)) {
            mergeData[key] = clonedeep(val[key]);
          }
        });
        mergeData = { ...state.data, ...mergeData };
      } else if (props.inherit.type === 'exclude') {
        for (const key in val) {
          if (Object.hasOwnProperty.call(val, key) && !props.inherit.value.includes(key)) {
            mergeData[key] = clonedeep(val[key]);
          }
        }
      }
    }
    state.data = mergeData;
  }, {
    deep: true,
    immediate: true,
  });

  watch(() => state.data, (val) => {
    if (props.initApi && proxy.shouldComponentUpdate && proxy.shouldComponentUpdate(val)) {
      proxy.onComponentUpdate(val);
    }
  });

  return state;
}
