<template>
  <div class="umis-form__formula" />
</template>
<script>
import {
  defineComponent, getCurrentInstance, watch, reactive,
} from 'vue';

import useDerivedProps from '../../../hooks/useDerivedProps';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/formula';

export default defineComponent({
  name: 'MisFormula',
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const iModelValue = reactive({});

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    });

    watch(() => props.initData, (val) => {
      if (proxy.$expressionEval(props.exp, val)) {
        iModelValue[props.name] = proxy.$expressionEval(props.exp, val);
        ctx.emit('update:modelValue', iModelValue[props.name]);
      }
    }, {
      deep: true,
      immediate: true,
    });

    return {
      ...useDerivedProps(props),
      iModelValue,
    };
  },
});
</script>
