<template>
  <el-page-header
    :icon="icon"
    :title="title"
    :content="getContent"
    @back="handleClick"
  />
</template>

<script>
import { defineComponent, computed, getCurrentInstance } from 'vue';
import { useRouter } from 'vue-router';
import { ElPageHeader } from 'element-plus';
import useInitData from '../../hooks/useInitData';
import mixinProps from '../props/page-header';
import initData from '../props/init-data';

export default defineComponent({
  name: 'MisPageHeader',
  components: {
    ElPageHeader,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const router = useRouter();
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);

    const handleClick = () => {
      router.push(props.url);
    };

    const getContent = computed(() => proxy.$renderTpl(props.content, state.data));

    return {
      getContent,
      handleClick,
    };
  },
});
</script>
