<template>
  <el-timeline
    v-if="iVisible"
    v-show="iHidden"
    :reverse="state.data.reverse"
  >
    <el-timeline-item
      v-for="(item, index) in state.data.rows"
      :key="index"
      :timestamp="item.timestamp"
      :placement="item.placement || placement"
      :type="item.type || type"
      :color="item.color || color"
      :size="item.size || size"
      :icon="item.icon || icon"
    >
      <mis-component
        :mis-name="body.renderer"
        :path="`${path}/${index}/${body.renderer}`"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :props="iAttrs(body)"
        :init-data="item"
      />
    </el-timeline-item>
  </el-timeline>
</template>

<script>
import { defineComponent } from 'vue';
import { ElTimeline, ElTimelineItem } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import useLinkage from '../../hooks/useLinkage';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/timeline';

export default defineComponent({
  name: 'MisTimeline',
  components: {
    ElTimeline,
    ElTimelineItem,
  },
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      ...useDerivedProps(props),
      ...useVisible(props),
      ...useLinkage(props),
      state,
    };
  },
});
</script>
