<template>
  <el-form-item
    v-show="iHidden"
    v-if="iVisible"
    :label="props.label"
    :prop="props.prop || props.name"
    :required="props.required"
    :class="[props.className, { 'umis-form__formula': props.renderer === 'mis-formula' }]"
    :rules="props.rules"
  >
    <template
      v-if="props.label"
      #label
    >
      <span class="umis-form__field">
        <span class="umis-form__field__label">{{ props.label }}</span>
        <el-tooltip v-if="props.tip">
          <i class="el-icon-info" />
          <template #content>
            <div v-html="props.tip" />
          </template>
        </el-tooltip>
      </span>
    </template>
    <template
      v-else
      #label
    >
      &nbsp;
    </template>
    <component
      v-if="!error"
      v-bind="props"
      :is="props.renderer"
      v-model="iModelValue[name]"
      :path="path"
      :track="path"
      :track-id="props.renderer"
      :name="props.name"
      :init-data="state.data"
      :disabled="iDisabled"
      :action="action"
      :field-changed="fieldChanged"
    />
    <mis-error v-else :error-info="errorInfo" :path="path" />
  </el-form-item>
</template>

<script>
import {
  defineComponent, watch, reactive, getCurrentInstance, onMounted, onBeforeUnmount, onErrorCaptured, ref, computed,
} from 'vue';
import { ElTooltip, ElFormItem } from 'element-plus';

import useInitData from '../../../hooks/useInitData';
import useVisible from '../../../hooks/useVisible';
import initData from '../../props/init-data';
import visibleProps from '../../props/visible';
import mixinProps from '../../props/form/field';

export default defineComponent({
  name: 'MisField',
  components: {
    ElTooltip,
    ElFormItem,
  },
  mixins: [mixinProps, visibleProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { iVisible, iHidden, iDisabled } = useVisible(props, ctx);
    const iModelValue = reactive({});
    const error = ref('');
    const errorInfo = computed(() => {
      if (error.value) {
        return {
          props: props.props,
          error: error.value,
        };
      }
      return {};
    });

    const handleVisible = (visible, name) => {
      if (props.name === name && props.handleInvisible) {
        props.handleInvisible(visible, name);
      }
    };

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
      props.fieldChanged && props.fieldChanged();
    });

    watch(() => props.initData, (val) => {
      state.data = val;
    }, {
      deep: true,
    });

    onErrorCaptured((err, vm, info) => {
      error.value = `'${err.message}' is found in ${props.misName} renderer`;
      console.error(err, vm, info);
      return false;
    });

    onMounted(() => {
      proxy.$eventHub.$on('mis-field:visible', handleVisible);
    });

    onBeforeUnmount(() => {
      proxy.$eventHub.$off('mis-field:visible', handleVisible);
    });

    return {
      errorInfo,
      error,
      iModelValue,
      iHidden,
      iVisible,
      iDisabled,
      state,
    };
  },
});
</script>
