<template>
  <el-input
    v-model="iModelValue[name]"
    :name="name"
    :label="label"
    :type="type"
    :size="size"
    :disabled="disabled"
    :readonly="readonly"
    :clearable="clearable"
    :min="minlength"
    :max="maxlength"
    :prefix-icon="prefixIcon"
    :suffix-icon="suffixIcon"
    :placeholder="placeholder"
    :show-password="showPassword"
    :show-word-limit="showWordLimit"
  >
    <template
      v-if="prepend"
      #prepend
    >
      <mis-component
        :mis-name="prepend.renderer"
        :path="`${path}/${prepend.renderer}`"
        :props="iAttrs(prepend)"
        :header="iHeader(prepend)"
        :body="iBody(prepend)"
        :footer="iFooter(prepend)"
        :init-data="state.data"
        v-bind="iAttrs(prepend)"
      />
    </template>
    <template
      v-if="append"
      #append
    >
      <mis-component
        :mis-name="append.renderer"
        :path="`${path}/${append.renderer}`"
        :props="iAttrs(append)"
        :header="iHeader(append)"
        :body="iBody(append)"
        :footer="iFooter(append)"
        :init-data="state.data"
        v-bind="iAttrs(append)"
      />
    </template>
  </el-input>
</template>
<script>
import { defineComponent, reactive, watch } from 'vue';
import { ElInput } from 'element-plus';

import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/input';

export default defineComponent({
  name: 'MisInput',
  components: {
    ElInput,
  },
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    return {
      ...useDerivedProps(props),
      iModelValue,
      state,
    };
  },
});
</script>
