<template>
  <div
    v-if="iVisible"
    v-show="iHidden"
    class="umis-image__container"
    :class="classname"
  >
    <template v-if="Object.prototype.toString.call(src) === '[object Array]'">
      <el-image
        v-for="(item, index) in src"
        :key="index"
        :src="item"
        :lazy="lazy"
        :fit="fit"
      >
        <template
          v-if="error"
          #error
        >
          {{ $renderTpl(error) }}
        </template>
        <template
          v-if="placeholder"
          #placeholder
        >
          {{ $renderTpl(placeholder) }}
        </template>
      </el-image>
    </template>
    <template v-else>
      <el-image
        :src="src"
        :lazy="lazy"
        :fit="fit"
        :preview-src-list="previews"
      >
        <template
          v-if="error"
          #error
        >
          {{ $renderTpl(error) }}
        </template>
        <template
          v-if="placeholder"
          #placeholder
        >
          {{ $renderTpl(placeholder) }}
        </template>
      </el-image>
    </template>
  </div>
</template>
<script>
import { defineComponent } from 'vue';
import { ElImage } from 'element-plus';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import mixinProps from '../props/image';

export default defineComponent({
  name: 'MisImage',
  components: {
    ElImage,
  },
  mixins: [mixinProps, visible],
  setup(props) {
    return {
      ...useVisible(props)
    };
  },
});
</script>
