<template>
  <el-rate
    v-model="iModelValue[name]"
    :disabled="disabled"
    :allowHalf="allowHalf"
    :lowThreshold="lowThreshold"
    :highThreshold="highThreshold"
    :colors="colors"
    :voidColor="voidColor"
    :disabledVoidColor="disabledVoidColor"
    :iconClasses="iconClasses"
    :voidIconClass="voidIconClass"
    :disabledVoidIconClass="disabledVoidIconClass"
    :textColor="textColor"
    :showText="showText"
    :showScore="showScore"
    :texts="texts"
    :scoreTemplate="scoreTemplate"
  />
</template>
<script>
import {defineComponent, reactive, watch} from 'vue';
import { ElRate } from 'element-plus';
import useInitData from '../../../hooks/useInitData';
import useVisible from '../../../hooks/useVisible';
import visible from '../../props/visible';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/rate';

export default defineComponent({
  name: 'MisRate',
  components: {
    ElRate
  },
  mixins: [mixinProps, visible, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val || 0;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    return {
      ...useVisible(props),
      iModelValue,
      state,
    };
  }
});
</script>
