<template>
  <span class="umis-form__datepicker">
    <el-date-picker
      v-model="iModelValue[name]"
      :name="name"
      :type="type"
      :readonly="readonly"
      :disabled="disabled"
      :editable="editable"
      :clearable="clearable"
      :size="size"
      :placeholder="placeholder"
      :start-placeholder="startPlaceholder"
      :end-placeholder="endPlaceholder"
      :value-format="format"
      :align="align"
      :prefix-icon="prefixIcon"
      :unlink-panels="unlinkPanels"
      :clear-icon="clearIcon"
    />
  </span>
</template>
<script>
import { defineComponent, watch, reactive } from 'vue';
import { ElDatePicker } from 'element-plus';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/datepicker';

export default defineComponent({
  name: 'MisDatePicker',
  components: {
    ElDatePicker,
  },
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    return {
      iModelValue,
      state,
    };
  },
});
</script>
