<template>
  <el-header
    :class="[classname, iComputedClass]"
    :height="`${iHeight}px`"
    class="umis-header__container"
  >
    <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
      <template
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
      >
        <mis-component
          v-bind="iAttrs(child)"
          :mis-name="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <mis-component
      v-if="Object.prototype.toString.call(body) === '[object Object]'"
      v-bind="iAttrs(body)"
      :mis-name="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
  </el-header>
</template>

<script>
import {
  defineComponent, ref, computed, watch, getCurrentInstance,
} from 'vue';
import { ElHeader } from 'element-plus';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/header';

export default defineComponent({
  name: 'MisHeader',
  components: {
    ElHeader,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iHeight = ref('');
    const iComputedClass = computed(() => {
      if (props.computedClass) {
        return proxy.$expressionEval(props.computedClass, state.data);
      }
      return '';
    });

    watch(() => props.height, (val) => {
      if (val) {
        iHeight.value = val;
      }
    }, { immediate: true });

    return {
      ...useDerivedProps(),
      iComputedClass,
      iHeight,
      state,
    };
  },
});
</script>
