<template>
  <span
    v-if="iVisible"
    v-show="iHidden"
    :class="`umis-component__fragment ${classname}`"
  >
    <el-popover
      v-if="popupType === 'over'"
      :title="popoverTitle"
      :content="popoverContent"
      :trigger="popoverTrigger"
      :placement="popoverPlacement"
      :popper-class="popperClass"
      :width="popperWidth"
    >
      <template #reference>
        <el-button
          v-loading="state.loading"
          :class="classname"
          :size="size"
          :type="type"
          :plain="plain"
          :round="round"
          :circle="circle"
          :icon="icon"
          :disabled="state.loading || iDisabled"
        >
          {{ renderText }}
        </el-button>
      </template>
      <mis-component
        v-if="body"
        :mis-name="body.renderer"
        :path="`${path}/${body.renderer}`"
        :body="iBody(body)"
        :header="iHeader(body)"
        :footer="iFooter(body)"
        :props="iAttrs(body)"
        :init-data="iData(body)"
      />
    </el-popover>
    <el-popconfirm
      v-else-if="popupType === 'confirm'"
      :confirm-button-text="confirmBtnText"
      :cancel-button-text="confirmCancelBtnText"
      :confirm-button-type="confirmBtnType"
      :cancel-button-type="confirmCancelBtnType"
      :icon="confirmIcon"
      :icon-color="confirmIconColor"
      :hide-icon="confirmHideIcon"
      :title="confirmTitle"
      @confirm="onClick"
    >
      <template #reference>
        <el-button
          v-loading="state.loading"
          :class="classname"
          :size="size"
          :type="type"
          :plain="plain"
          :round="round"
          :circle="circle"
          :icon="icon"
          :disabled="state.loading || iDisabled"
        >
          {{ renderText }}
        </el-button>
      </template>
    </el-popconfirm>
    <el-tooltip
      v-else-if="popupType === 'tip'"
      :disabled="tipDisabled"
      :effect="tipEffect"
      :content="tipContent"
      :placement="tipPlacement"
      :popper-class="popperClass"
      :append-to-body="true"
    >
      <el-button
        v-loading="state.loading"
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
        :icon="icon"
        :disabled="state.loading"
        @click="onClick"
      >
        {{ text }}
      </el-button>
    </el-tooltip>
    <el-badge
      v-else-if="popupType === 'badge'"
      :value="badgeText"
      :class="badgeClass"
    >
      <el-button
        v-loading="state.loading"
        :class="classname"
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
        :icon="icon"
        :disabled="state.loading || iDisabled"
        @click="onClick"
      >
        {{ text }}
      </el-button>
    </el-badge>
    <el-button-group
      v-else-if="popupType === '' && actions"
      v-loading="state.loading"
      :class="classname"
    >
      <template
        v-for="(item, index) in actions"
        :key="`${path}/${index}`"
      >
        <el-button
          v-bind="item"
          :index="index"
          :disabled="state.loading || iDisabled"
          @click="onClick"
        >
          {{ item.text }}
        </el-button>
      </template>
    </el-button-group>
    <el-button
      v-else-if="typeof size === 'string'"
      v-loading="state.loading"
      :class="classname"
      :size="size"
      :type="type"
      :plain="plain"
      :round="round"
      :circle="circle"
      :icon="icon"
      :disabled="iDisabled || state.loading"
      @click="onClick"
    >
      {{ renderText }}
    </el-button>
    <el-icon
      v-else
      :class="icon"
      :size="size"
      @click="onClick"
    />
  </span>
</template>
<script>
import {defineComponent, computed, getCurrentInstance, watch, nextTick, onBeforeUnmount} from 'vue';
import {
  ElIcon,
  ElPopover,
  ElPopconfirm,
  ElTooltip,
  ElBadge,
  ElButton,
  ElButtonGroup,
} from 'element-plus';

import useInitData from '../../hooks/useInitData';
import useDerivedProp from '../../hooks/useDerivedProps';
import useLinkage from '../../hooks/useLinkage';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/action';

export default defineComponent({
  name: 'MisAction',
  components: {
    ElIcon,
    ElPopover,
    ElPopconfirm,
    ElTooltip,
    ElBadge,
    ElButton,
    ElButtonGroup,
  },
  mixins: [mixinProps, initData, visible],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { onLinkageTrigger } = useLinkage(props);
    const renderText = computed(() => proxy.$renderTpl(props.text, props.initData));
    let activeIndex;

    const iDisabled = computed(() => proxy.$expressionEval(props.disabledOn, state.data));

    const onClick = () => {
      const { attributes } = event.currentTarget;
      let actionType = props.actionType;
      let remoteComponent;

      if (props.actions) {
        activeIndex = attributes.index && attributes.index.value;
        remoteComponent = attributes.remotecomponent && attributes.remotecomponent.value;
        actionType = props.actions[activeIndex].actionType;
      }
      if (remoteComponent || props.remoteComponent) {
        proxy.$eventHub.$emit(
          'mis-component:remoteComponent',
          actionType,
          remoteComponent || props.remoteComponent,
          handleAfterAction,
        );
      } else if (typeof activeIndex === 'string') {
        props.action(
          {
            actions: true,
            path: props.path,
            ...props.actions[activeIndex],
          },
          props.initData,
          handleAfterAction,
        );
      } else {
        const {
          renderer, actionApi, header, body, footer,
        } = props;
        const { ...other } = ctx.attrs;
        props.action(
          {
            path: props.path,
            renderer,
            actionType,
            actionApi,
            header,
            body,
            footer,
            ...other,
          },
          props.initData,
          handleAfterAction,
        );
      }
    };

    const handleAfterAction = (data) => {
      if (props.actionApi && props.actionApi.method === 'put') {
        Object.assign(state.data, data);
      } else {
        state.data = data;
      }
      if (props.afterAction && typeof props.afterAction === 'function') {
        props.afterAction();
      }
      if (props.redirect) {
        proxy.$parent.filterAction({actionType: 'mis-url', url: props.redirect}, state.data);
      }
      if (props.reload) {
        return proxy.$eventHub.$emit('mis-component:reload', props.reload, state.data);
      }
      if (props.target) {
        onLinkageTrigger(props.target, state.data);
      }
    };

    watch(() => props.actionType, val => {
      if (proxy.$umisConfig.editable && ['mis-dialog', 'mis-drawer'].includes(val)) {
        nextTick(() => {
          proxy.$eventHub.$emit('mis-portal:show', val, `${ctx.attrs.track}/body`, props.body);
        });
      }
    }, {
      immediate: true
    });
    onBeforeUnmount(() => {
      if (proxy.$umisConfig.editable && ['mis-dialog', 'mis-drawer'].includes(props.actionType)) {
        nextTick(() => {
          proxy.$eventHub.$emit('mis-portal:hidden', props.actionType, `${ctx.attrs.track}/body`, props.body);
        });
      }
    });

    return {
      ...useInitData(props),
      ...useDerivedProp(props),
      ...useLinkage(props),
      ...useVisible(props),
      onClick,
      iDisabled,
      renderText,
      state
    };
  },
});
</script>
