<template>
  <el-tabs
    v-if="iVisible"
    v-show="iHidden"
    v-model="state.data.activeName"
    :path="`${path}`"
    :type="type"
    :style="tabStyle"
    :stretch="stretch"
    :tab-position="tabPosition"
    class="umis-tabs__container"
  >
    <el-tab-pane
      v-for="(item, index) in panels"
      :key="`${path}/${index}`"
      :label="item.label"
      :name="item.name"
      :lazy="true"
      v-bind="iAttrs(item)"
    >
      <template #label>
        <i
          v-if="item.icon"
          :class="item.icon"
        />
        {{ item.label }}
      </template>
      <template v-if="isPanelAlive(item)">
        <template
          v-if="Object.prototype.toString.call(item.body) === '[object Object]'"
        >
          <mis-component
            v-bind="iAttrs(item.body)"
            :mis-name="item.body.renderer"
            :path="`${path}/panels/${index}/${item.body.renderer}`"
            :track="`${path}/panels/${index}/body`"
            :props="iAttrs(item.body)"
            :header="iHeader(item.body)"
            :body="iBody(item.body)"
            :footer="iFooter(item.body)"
            :init-data="state.data"
          />
        </template>
        <template v-else>
          <template
            v-for="(child, count) in item.body"
            :key="`${path}/${count}/${child.renderer}`"
          >
            <mis-component
              v-bind="iAttrs(child)"
              :mis-name="child.renderer"
              :path="`${path}/panels/${index}/body/${count}/${child.renderer}`"
              :track="`${path}/panels/${index}/body/${count}`"
              :props="iAttrs(child)"
              :header="iHeader(child)"
              :body="iBody(child)"
              :footer="iFooter(child)"
              :init-data="state.data"
            />
          </template>
        </template>
      </template>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import { defineComponent, watch } from 'vue';
import { ElTabs, ElTabPane } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/tabs';

export default defineComponent({
  name: 'MisTabs',
  components: {
    ElTabs,
    ElTabPane,
  },
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const state = useInitData(props);
    const isPanelAlive = (item) => {
      if (item.keepAlive === false) {
        return state.data.activeName === item.name;
      }
      return true;
    };

    watch(() => props.activeName, (val) => {
      state.data.activeName = val;
    }, {
      immediate: true,
    });

    return {
      ...useDerivedProps(),
      ...useVisible(props),
      isPanelAlive,
      state,
    };
  },
});
</script>
