<template>
  <el-affix
    :target="target"
    :offset="offset"
    :position="position"
    :z-index="zIndex"
  >
    <mis-component
      v-bind="iAttrs(body)"
      :mis-name="body.renderer"
      :path="`${path}/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
  </el-affix>
</template>
<script>
import { defineComponent } from 'vue';
import { ElAffix } from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/affix';

export default defineComponent({
  name: 'MisAffix',
  components: {
    ElAffix,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
