<template>
  <span class="umis-quick-editor">
    <el-popover
      :width="width"
      :title="title"
      :placement="placement"
      trigger="click"
    >
      <template #reference>
        <span class="el-link el-link--info is-underline">
          <span class="el-link--inner">
            {{ renderText }}
            <i class="el-icon-edit" />
          </span>
        </span>
      </template>
      <el-form
        ref="quickeditor"
        class="umis-quick-editor__form"
        size="mini"
        inline
        :model="formdata"
      >
        <el-form-item
          prop="keyword"
          :rules="rules"
        >
          <el-input v-model="formdata.keyword" />
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="onSubmit"
          >
            更新
          </el-button>
        </el-form-item>
      </el-form>
    </el-popover>
  </span>
</template>

<script>
import {
  computed, defineComponent, reactive, getCurrentInstance, watch,
} from 'vue';
import {
  ElLink, ElForm, ElFormItem, ElInput, ElButton,
} from 'element-plus';

import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/quick-editor';

export default defineComponent({
  name: 'MisQuickedit',
  components: [ElLink, ElForm, ElFormItem, ElInput, ElButton],
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const renderText = computed(() => proxy.$renderTpl(props.label, state.data));
    const formdata = reactive({
      keyword: state.data[props.keyword] || '',
    });

    const onSubmit = () => {
      proxy.$refs.quickeditor.validate((valid) => {
        if (valid) {
          const { actionApi } = props;
          const { ...other } = ctx.attrs;
          let context = {};
          context[props.keyword] = formdata.keyword;
          context = { ...state.data, ...context };

          props.action(
            {
              actionType: 'mis-ajax',
              actionApi,
              ...other,
            },
            context,
          );
        } else {
          proxy.$notice({
            title: '警告',
            message: '报错失败！',
            type: 'error',
          });
          return false;
        }
      });
    };

    watch(() => state, (val) => {
      formdata.keyword = state.data[props.keyword];
    }, {
      deep: true,
    });

    return {
      onSubmit,
      state,
      renderText,
      formdata,
    };
  },
});
</script>
