import { getCurrentInstance } from 'vue';

export default function (props = {}) {
  const { proxy } = getCurrentInstance();
  const iProps = props;

  const iHeader = (props = iProps) => props.header;

  const iBody = (props = iProps) => props.body;

  const iFooter = (props = iProps) => props.footer;

  const iData = (props = iProps) => {
    if (proxy.state && proxy.state.data) {
      const data = proxy.state.data || {};
      const initData = props.initData || {};
      return Object.assign(data, initData);
    }
    return props.initData;
  };

  const iAttrs = (props = iProps) => {
    const {
      header, body, footer, ...other
    } = props;

    return other;
  };

  return {
    iHeader,
    iBody,
    iFooter,
    iAttrs,
    iData,
  };
}
