<template>
  <div
    v-if="title"
    class="umis-avatar__container"
  >
    <div class="umis-avatar__logo">
      <el-avatar
        v-if="!icon"
        :class="classname"
        :icon="icon"
        :shape="shape"
        :size="size"
        :fit="fit"
        :src="isComputedSrc ? computedSrc : src"
        :src-set="srcSet"
        :alt="alt"
        @click="onClick"
      />
      <mis-icon
        v-else
        :path="`${path}/icon`"
        :icon="icon"
        :size="size"
        :invert="invert"
        :color="color"
        :bgColor="bgColor"
        :borderRadius="borderRadius"
      />
    </div>
    <div class="umis-avatar__content">
      <div
        v-if="title"
        class="umis-avatar__title"
        :title="renderTitle"
      >
        {{ renderTitle }}
      </div>
      <div
        v-if="subTitle"
        class="umis-avatar__subtitle"
        :title="renderSubTitle"
      >
        {{ renderSubTitle }}
      </div>
      <div
        v-if="description"
        class="umis-avatar__description"
      >
        {{ renderDescription }}
      </div>
    </div>
  </div>
  <el-avatar
    v-else
    :class="classname"
    :shape="shape"
    :size="size"
    :fit="fit"
    :src="isComputedSrc ? computedSrc : src"
    :src-set="srcSet"
    :alt="alt"
    @click="onClick"
  />
</template>
<script>
import {
  defineComponent, computed, ref, reactive, onBeforeMount, getCurrentInstance,
} from 'vue';
import { ElAvatar } from 'element-plus';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/avatar';

export default defineComponent({
  name: 'MisAvatar',
  components: {
    ElAvatar,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const computedSrc = ref();
    let iconProps = reactive({});
    const renderTitle = computed(() => {
      if (props.title) {
        return proxy.$renderTpl(props.title, state.data);
      }
      return undefined;
    });
    const renderSubTitle = computed(() => {
      if (props.subTitle) {
        return proxy.$renderTpl(props.subTitle, state.data);
      }
      return undefined;
    });
    const renderDescription = computed(() => {
      if (props.description) {
        return proxy.$renderTpl(props.description, state.data);
      }
      return undefined;
    });

    const onClick = () => {
      const {
        renderer, content, url, redirect, blank,
      } = proxy.$attrs;
      if (props.action && typeof props.action === 'function') {
        props.action(
          {
            renderer, content, url, redirect, blank, actionType: props.actionType,
          },
          state.data,
        );
      }
    };
    onBeforeMount(() => {
      if (props.src) {
        computedSrc.value = proxy.$compiledUrl(props.src, state.data);
      }
      iconProps = {
        size: props.size,
        color: props.color,
        bgColor: props.bgColor,
        icon: props.icon,
        invert: props.invert,
        borderRadius: props.borderRadius,
        action: props.action,
        actionType: props.actionType,
      };
    });

    return {
      computedSrc,
      iconProps,
      renderTitle,
      renderSubTitle,
      renderDescription,
      onClick,
    };
  },
});
</script>
