<template>
  <div class="umis-editor__container">
    <transition name="el-zoom-in-center">
      <el-alert
        v-if="state.showErrorBoundary"
        class="umis-editor__container__error-info"
        :title="state.errorInfo"
        type="error"
        show-icon
        @close="closeErrorInfo"
      />
    </transition>
    <div
      v-if="state.iJson"
      ref="editor"
      class="monaco-editor"
    />
    <toolmaker
      v-if="!state.iJson"
      ref="toolmaker"
      :path="`${path}/toolmaker`"
    />
    <transition name="el-zoom-in-bottom">
      <div
        v-if="(!state.showErrorBoundary && editable)"
        class="umis-editor__container__tools"
      >
        <el-button
          v-if="state.iJson"
          size="mini"
          type="primary"
          @click="onChange"
        >
          拖拽模式
        </el-button>
        <el-button
          v-if="!state.iJson"
          size="mini"
          type="primary"
          @click="onChange"
        >
          JSON模式
        </el-button>
        <el-button
          size="mini"
          type="primary"
          @click="onSave"
        >
          保存到本地
        </el-button>
        <el-button
          size="mini"
          type="danger"
          @click="onSaveRemote"
        >
          保存到远程
        </el-button>
      </div>
    </transition>
  </div>
</template>
<script>
import {getCurrentInstance, defineAsyncComponent, defineComponent, reactive, ref, watch, onMounted, nextTick} from 'vue';
import {ElButton, ElAlert} from 'element-plus';

import {monaco} from '../../utils/monaco';
import initApi from '../../renderer/props/init-api';

// const monaco = window.monaco;
const DEFAULT_CONFIG = {
  theme: 'vs',
  inherit: false,
  fontSize: '14px',
  autoIndent: true,
  formatOnType: true,
  formatOnPaste: true,
  selectOnLineNumbers: true,
  scrollBeyondLastLine: false,
  folding: true,
  automaticLayout: true,
  minimap: {
    enabled: false,
  },
};

export default defineComponent({
  name: 'MisEditor',
  components: {
    Toolmaker: defineAsyncComponent(() => import('./toolmaker.vue')),
    ElButton,
    ElAlert,
  },
  mixins: [initApi],
  props: {
    path: {
      type: String,
      required: true,
    },
    schema: {
      type: Object,
      required: false,
      default: function() {
        return {};
      },
    },
    editable: {
      type: Boolean,
      required: false,
      default: false,
    },
    isJson: {
      type: Boolean,
      required: false,
      default: true,
    },
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      errorInfo: '',
      iJson: false,
      iSchema: {},
      pageInfo: {},
      showErrorBoundary: false,
    });
    const iSchema = ref('');
    let editor;
    const initEditor = () => {
      if (props.editable) {
        const { pageSchema } = window.UMIS;
        editor = monaco.editor.create(proxy.$.refs.editor, {
          ...DEFAULT_CONFIG,
          language: 'json',
          theme: 'vs-dark',
        });
        updateSchema(pageSchema);
      } else {
        editor = monaco.editor.create(proxy.$.refs.editor, {
          ...DEFAULT_CONFIG,
          theme: 'vs',
          language: 'json',
        });
        updateSchema();
      }
    };
    const handleFormatSchema = (editor) => {
      const timer = setTimeout(() => {
        editor
          .getAction(['editor.action.formatDocument'])
          .run()
          .then(() => {
            clearTimeout(timer);
          })
      }, 200);
    };
    const onChange = () => {
      state.iJson = !state.iJson;
      if (state.iJson) {
        nextTick(() => {
          initEditor();
        });
      }
    };
    const updateSchema = (pageSchema) => {
      if (pageSchema) {
        iSchema.value = pageSchema;
      }
      editor.setValue(JSON.stringify(iSchema.value));
      handleFormatSchema(editor);
    };
    const onSaveRemote = () => {
      onSave().then((pageSchema) => {
        proxy.$api
          .slientApi()
          .put('/api/page', {
            pageSchema: JSON.stringify(pageSchema),
            ...state.pageInfo,
          })
          .then((res) => {
            console.log(res);
          })
          .catch((error) => {
            proxy.$message({
              message: error.message,
              showClose: true,
              type: ' error',
            });
          });
      });
    };
    const onSave = () => new Promise((resolve, reject) => {
      try {
        let schema;
        if (state.iJson) {
          const json = editor.getValue();
          schema = JSON.parse(json);
          handleFormatSchema(editor);
        } else {
          schema = proxy.$.refs.toolmaker.schemaInstance;
        }
        proxy.$eventHub.$emit('mis-schema:change', schema);
        proxy.$message({
          message: '保存成功',
          showClose: true,
          type: 'success',
        });
        resolve(schema);
      } catch (e) {
        state.errorInfo = e;
        state.showErrorBoundary = true;
        state.errorInfoTimer = setTimeout(() => {
          closeErrorInfo();
          clearTimeout(state.errorInfoTimer);
        }, 3500);
        reject(e);
      }
    });
    const closeErrorInfo = () => {
      state.showErrorBoundary = false;
    };

    watch(() => props.schema, (val) => {
      iSchema.value = val;
    }, {
      immediate: true,
    });

    watch(() => props.isJson, val => {
      state.iJson = val;
    }, {
      immediate: true
    });
    onMounted(() => {
      const { pageInfo } = window.UMIS;
      state.pageInfo = pageInfo;
      const timer = setTimeout(() => {
        if (state.iJson) {
          initEditor();
        }
        clearTimeout(timer);
      });
    });
    return {
      initEditor,
      onChange,
      updateSchema,
      onSave,
      onSaveRemote,
      closeErrorInfo,
      state,
    };
  },
});
</script>
