<template>
  <el-upload
    ref="upload"
    with-credentials
    :action="actionUrl"
    :on-exceed="handleExceed"
    :on-success="handleSuccess"
    :on-error="handleError"
    :file-list="fileList"
    :limit="limit"
    :headers="headers"
    :auto-upload="false"
    :data="params"
  >
    <el-button
      size="small"
      type="primary"
    >
      选取文件
    </el-button>
    <el-button
      size="small"
      type="success"
      @click="submitUpload"
    >
      上传到服务器
    </el-button>
    <template
      v-if="tip"
      #tip
    >
      <div class="el-upload__tip">
        {{ tip }}
      </div>
    </template>
  </el-upload>
</template>

<script>
import {defineComponent, getCurrentInstance, onMounted, reactive, ref} from 'vue';
import { ElUpload, ElButton } from 'element-plus';
import useInitData from '../../../hooks/useInitData';
import mixinProps from '../../props/form/upload';
import initData from '../../props/init-data';

export default defineComponent({
  name: 'MisUpload',
  components: {
    ElUpload,
    ElButton,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const headers = reactive({});
    const fileList = reactive([]);

    const submitUpload = () => {
      event.stopPropagation();
      event.preventDefault();

      proxy.$refs.upload.submit();
    };
    const handleExceed = (files, fileList) => {
      proxy.$message.warning(`当前限制选择${props.limit}个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    };
    const handleSuccess = (res) => {
      if (res.data) {
        const feedbackData = {};
        Object.keys(props.feedbackConfig).forEach(item => {
          feedbackData[props.feedbackConfig[item]] = res.data[item];
        });
        props.action(feedbackData);
      } else {
        proxy.$confirm('上传失败，请重试');
      }
    };
    const handleError = (err) => {
      if (err && err.msg) {
        proxy.$confirm(err.msg);
      } else {
        proxy.$confirm('上传失败，请重试');
      }
    };

    onMounted(() => {
      const cmsToken = localStorage.getItem('cmsToken');
      headers.value = {
        Authorization: cmsToken
      };
    });
    return {
      submitUpload,
      handleExceed,
      handleSuccess,
      handleError,
      fileList,
      headers,
      state
    };
  },
});
</script>
