import { computed, getCurrentInstance } from 'vue';

export default function (props, ctx) {
  const { proxy } = getCurrentInstance();
  let visibleCache;

  const iHidden = computed(() => {
    if (props.hiddenOn) {
      return !proxy.$expressionEval(props.hiddenOn, proxy.state.data);
    }
    return true;
  });
  const iDisabled = computed(() => {
    if (props.disabledOn) {
      return proxy.$expressionEval(props.disabledOn, proxy.state.data);
    }
    return false;
  });
  const iVisible = computed(() => {
    if (props.visibleOn) {
      const visible = proxy.$expressionEval(props.visibleOn, proxy.state.data);
      if (visibleCache !== visible) {
        visibleCache = visible;
        proxy.$eventHub.$emit('mis-field:visible', visible, props.name);
      }
      return visible;
    }
    return true;
  });

  return {
    iHidden,
    iDisabled,
    iVisible,
  };
}
