<template>
  <el-progress
    :type="type"
    :width="width"
    :color="color"
    :status="status"
    :duration="duration"
    :indeterminate="indeterminate"
    :show-text="showText"
    :percentage="iPercentage"
    :stroke-width="strokeWidth"
    :text-inside="textInside"
    :stroke-linecap="strokeLinecap"
  />
</template>

<script>
import { defineComponent, getCurrentInstance, computed } from 'vue';
import { ElProgress } from 'element-plus';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/progress';

export default defineComponent({
  name: 'MisProgress',
  components: {
    ElProgress,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iPercentage = computed(() => {
      let value = props.percentage;
      if (props.exp) {
        value = proxy.$expressionEval(props.exp, state.data);
      }
      return Number(value);
    });

    return {
      iPercentage,
      state
    };
  }
});
</script>
