<template>
  <div class="umis-component__fragment">
    <el-drawer
      v-model="iVisible"
      :close-on-press-escape="closeOnPressEscape"
      :modal="modal"
      :direction="direction"
      :show-close="showClose"
      :size="width"
      :with-header="withHeader"
      destroy-on-close
      :custom-class="`umis-popup__container ${classname}`"
      @close="onClose"
    >
      <template
        v-if="header"
        #title
      >
        <mis-component
          :mis-name="header.renderer"
          :path="`${path}/header/${header.renderer}`"
          :action="onClose"
          :after-action="onClose"
          :props="iAttrs(header)"
          :header="iHeader(header)"
          :body="iBody(header)"
          :footer="iFooter(header)"
          :init-data="state.data"
        />
      </template>
      <el-main
        v-if="body"
        class="umis-popup__container__body"
      >
        <template
          v-if="Object.prototype.toString.call(body) === '[object Array]'"
        >
          <template
            v-for="(item, index) in body"
            :key="`${path}/body/${index}/${item.renderer}`"
          >
            <mis-component
              :mis-name="item.renderer"
              :path="`${path}/body/${index}/${item.renderer}`"
              :action="onClose"
              :props="iAttrs(item)"
              :header="iHeader(item)"
              :body="iBody(item)"
              :footer="iFooter(item)"
              :init-data="state.data"
            />
          </template>
        </template>
        <mis-component
          v-else
          :mis-name="body.renderer"
          :path="`${path}/body/${body.renderer}`"
          :action="onClose"
          :props="iAttrs(body)"
          :header="iHeader(body)"
          :body="iBody(body)"
          :footer="iFooter(body)"
          :init-data="state.data"
        />
      </el-main>
      <div
        v-if="footer"
        class="umis-popup__container__footer"
      >
        <template
          v-for="(item, index) in footer"
          :key="`${path}/footer/${index}/${item.renderer}`"
        >
          <mis-component
            :mis-name="item.renderer"
            :path="`${path}/footer/${index}/${item.renderer}`"
            :footer="item.footer"
            :props="iAttrs(item)"
            :init-data="state.data"
            :after-action="closable ? onClose : () => {}"
            :action="onClose"
          />
        </template>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import { defineComponent, ref } from 'vue';
import { ElDrawer, ElMain } from 'element-plus';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/drawer';

export default defineComponent({
  name: 'MisDrawer',
  components: {
    ElMain,
    ElDrawer,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    const iVisible = ref(props.visible);
    const onClose = () => {
      iVisible.value = false;
      props.onPopupInvisible && props.onPopupInvisible(props.path);
    };

    return {
      iVisible,
      onClose,
      state,
      ...useDerivedProps(props),
    };
  },
});
</script>
