<template>
  <div class="umis-steps__container">
    <el-steps
      :path="path"
      :active="iActive"
      :simple="true"
      :name="name"
      :direction="direction"
      :align-center="alignCenter"
      :finish-status="finishStatus"
      :process-status="processStatus"
    >
      <template
        v-for="(item, index) in body"
        :key="index"
      >
        <el-step
          :title="item.title"
          :icon="item.icon"
          :status="item.status"
          :description="item.description"
        />
      </template>
    </el-steps>
    <div
      v-loading="!iShow"
      class="el-steps__main"
    >
      <mis-component
        v-if="iShow"
        ref="form"
        mis-name="mis-form"
        :index="iActive - 1"
        :path="`${path}/${iActive}`"
        :init-data="state.data"
        :props="{
          renderer: 'mis-form',
          controls: body[iActive - 1].controls
        }"
      />
    </div>
    <div class="el-steps__footer">
      <el-button-group>
        <el-button
          :disabled="iActive === 1 || state.loading"
          @click="previous"
        >
          上一步
        </el-button>
        <el-button
          v-if="iActive < body.length"
          :disabled="state.loading"
          @click="next"
        >
          下一步
        </el-button>
        <el-button
          v-if="iActive === body.length"
          v-loading="state.loading"
          :disabled="state.loading"
          @click="done"
        >
          完成
        </el-button>
      </el-button-group>
      <mis-component
        v-if="redirect && false"
        ref="redirect"
        mis-name="mis-action"
        :path="`${path}/mis-action`"
        :init-data="state.data"
        hidden-on="1 === 1"
        :props="{
          hiddenOn: '1===1',
          renderer: 'mis-action',
          actionType: 'mis-redirect',
          redirect: redirect,
        }"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent, watch, ref, nextTick, getCurrentInstance } from 'vue';
import { ElSteps, ElStep, ElButton, ElButtonGroup } from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import uselinkage from '../../hooks/useLinkage';
import useInitData from '../../hooks/useInitData';
import useInitApi from '../../hooks/useInitApi';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/steps';
export default defineComponent({
  name: 'MisSteps',
  components: {
    ElSteps,
    ElStep,
    ElButton,
    ElButtonGroup
  },
  mixins: [mixinProps, initData, initApi],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const { handleFetchApi } = useInitApi(props);
    const iActive = ref(1);
    const iShow = ref(true);
    const formdata = {};
    for (const key in props.body) {
      if (Object.hasOwnProperty.call(props.body, key)) {
        props.body[key].controls.forEach(control => {
          const renderer = control.renderer;
          const name = control.name;
          const value = control.value || '';
          if (name && proxy.$formItems.includes(renderer) && renderer !== 'mis-action') {
            formdata[name] = value;
          }
        });
      }
    }
    Object.assign(state.data, formdata);
    state.loading = false;
    const previous = () => {
      beforeUpdateActive().then(() => {
        iShow.value = false;
        if (iActive.value > 1) {
          --iActive.value;
        }
        mergeFormData();
        nextTick(() => {
          iShow.value = true;
        });
      }).catch();
    };
    const next = () => {
      beforeUpdateActive().then(() => {
        iShow.value = false;
        if (iActive.value < props.body.length) {
          ++iActive.value;
        }
        mergeFormData();
        nextTick(() => {
          iShow.value = true;
        });
      }).catch();
    };
    const done = () => {
      beforeUpdateActive().then(() => {
        state.loading = true;
        mergeFormData();
        handleFetchApi({
          url: props.actionApi.url,
          method: props.actionApi.method || 'post',
          params: state.data,
        }, () => {
          onAfterSubmit();
        }).finally(() => {
          state.loading = false;
        });
      }).catch();
    };
    const mergeFormData = () => {
      const data = proxy.$refs.form.$refs.component.state.data;
      Object.assign(state.data, data);
    };
    const beforeUpdateActive = () => {
      return new Promise((resolve) => {
        proxy.$refs.form.$refs.component.$refs.form.validate(valid => {
          if (valid) {
            resolve();
          }
        });
      });
    };
    const onAfterSubmit = () => {
      if (props.redirect) {
        proxy.$refs.redirect.$refs.component.onClick();
      } else if (props.reload) {
        proxy.$eventHub.$emit('mis-component:reload', props.reload);
      }
    };
    watch(() => props.active, val => {
      iActive.value = val || 1;
    }, {
      immediate: true
    });
    return {
      ...useDerivedProps(props),
      ...uselinkage(props),
      previous,
      next,
      done,
      iActive,
      iShow,
      state
    };
  },
});
</script>
