<template>
  <span class="umis-form__colorpicker">
    <el-color-picker
      v-model="iModelValue[name]"
      :disabled="disabled"
      :size="size"
      :show-alpha="showAlpha"
      :color-format="colorFormat"
      :popper-class="popperClass"
      :predefine="predefine"
    />
  </span>
</template>
<script>
import {defineComponent, reactive, watch} from 'vue';
import { ElColorPicker } from 'element-plus';
import useInitData from '../../../hooks/useInitData';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/colorpicker';

export default defineComponent({
  name: 'MisColorPicker',
  components: {
    ElColorPicker
  },
  mixins: [mixinProps, initData],
  setup(props, ctx) {
    const state = useInitData(props);
    const iModelValue = reactive({});

    watch(() => props.defaultValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val;
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val) => {
      ctx.emit('update:modelValue', val);
    });

    return {
      iModelValue,
      state,
    };
  }
});
</script>
