<template>
  <el-footer
    :class="classname"
    class="umis-footer__container"
  >
    <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
      <template
        v-for="(child, index) in body"
        :key="`${path}/${index}/${child.renderer}`"
      >
        <mis-component
          v-bind="iAttrs(child)"
          :mis-name="child.renderer"
          :path="`${path}/body/${index}/${child.renderer}`"
          :header="iHeader(child)"
          :body="iBody(child)"
          :footer="iFooter(child)"
          :props="iAttrs(child)"
          :init-data="state.data"
        />
      </template>
    </template>
    <mis-component
      v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
      v-bind="iAttrs(body)"
      :key="`${path}/${body.renderer}`"
      :mis-name="body.renderer"
      :path="`${path}/body/${body.renderer}`"
      :header="iHeader(body)"
      :body="iBody(body)"
      :footer="iFooter(body)"
      :props="iAttrs(body)"
      :init-data="state.data"
    />
  </el-footer>
</template>

<script>
import { defineComponent } from 'vue';
import { ElFooter } from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/footer';

export default defineComponent({
  name: 'MisMain',
  components: {
    ElFooter,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
