<template>
  <div class="umis-component__fragment">
    <el-dialog
      v-model="iVisible"
      :title="renderTitle"
      :width="width"
      :fullscreen="fullscreen"
      :modal="modal"
      :lock-scroll="lockScroll"
      :close-on-click-modal="closeOnModal"
      :show-close="showClose"
      :destroy-on-close="destroyOnClose"
      :custom-class="`umis-popup__container ${classname}`"
      @close="onClose"
    >
      <el-main
        v-if="body"
        class="umis-popup__container__body"
      >
        <mis-component
          v-if="Object.prototype.toString.call(body) === '[object Object]'"
          :mis-name="body.renderer"
          :path="`${path}/body/${body.renderer}`"
          :header="iHeader(body)"
          :body="iBody(body)"
          :footer="iFooter(body)"
          :props="iAttrs(body)"
          :init-data="state.data"
        />
        <template v-else>
          <template
            v-for="(item, index) in body"
            :key="`${path}/body/${index}/${item.renderer}`"
          >
            <mis-component
              :mis-name="item.renderer"
              :path="`${path}/body/${index}/${item.renderer}`"
              :header="iHeader(item)"
              :body="iBody(item)"
              :footer="iFooter(item)"
              :props="iAttrs(item)"
              :init-data="state.data"
            />
          </template>
        </template>
      </el-main>
      <div
        v-if="footer"
        class="umis-popup__container__footer"
      >
        <template
          v-if="Object.prototype.toString.call(footer) === '[object Array]'"
        >
          <template
            v-for="(item, index) in footer"
            :key="`${path}/footer/${index}/${item.renderer}`"
          >
            <mis-component
              :path="`${path}/footer/${index}/${item.renderer}`"
              :mis-name="item.renderer"
              :header="iHeader(item)"
              :body="iBody(item)"
              :footer="iFooter(item)"
              :props="iAttrs(item)"
              :init-data="state.data"
              :after-action="closable ? onClose : () => {}"
            />
          </template>
        </template>
        <template
          v-if="Object.prototype.toString.call(footer) === '[object Object]'"
        >
          <mis-component
            :mis-name="footer.renderer"
            :path="`${path}/footer/${footer.renderer}`"
            :header="iHeader(footer)"
            :body="iBody(footer)"
            :footer="iFooter(footer)"
            :props="iAttrs(footer)"
            :init-data="state.data"
            :after-action="closable ? onClose : () => {}"
          />
        </template>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  defineComponent, computed, ref, getCurrentInstance,
} from 'vue';
import { ElDialog, ElMain } from 'element-plus';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/dialog';

export default defineComponent({
  name: 'MisDialog',
  components: {
    ElMain,
    ElDialog,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iVisible = ref(props.visible);

    const onClose = () => {
      iVisible.value = false;
      props.onPopupInvisible && props.onPopupInvisible(props.path);
    };
    const renderTitle = computed(() => proxy.$renderTpl(props.title, props.initData));

    return {
      state,
      ...useDerivedProps(props),
      onClose,
      iVisible,
      renderTitle,
    };
  },
});
</script>
