<template>
  <el-calendar
    v-model="iModelValue[name]"
    class="umis-form__calendar"
  >
    <template
      v-if="body"
      #dateCell="{data}"
    >
      <div
        class="umis-form__calendar__body"
        :class="data.isSelected ? 'selected' : ''"
      >
        <div
          class="umis-form-calendar__body__index"
        >
          {{ data.day.split('-').slice(2)[0] }}
        </div>
        <mis-component
          :mis-name="body.renderer"
          :path="`${path}/${body.renderer}`"
          :props="iAttrs(body)"
          :header="iHeader(body)"
          :body="iBody(body)"
          :footer="iFooter(body)"
          :init-data="{...data, calendarData: calendarData[data.day]}"
          v-bind="iAttrs(body)"
        />
      </div>
    </template>
  </el-calendar>
</template>
<script>
import {defineComponent, onMounted, reactive, ref, watch} from 'vue';
import { ElCalendar } from 'element-plus';

import useLinkage from '../../../hooks/useLinkage';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitApi from '../../../hooks/useInitApi';
import useInitData from '../../../hooks/useInitData';
import initApi from '../../props/init-api';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/calendar';

export default defineComponent({
  name: 'MisCalendar',
  components: {
    ElCalendar
  },
  mixins: [mixinProps, initData, initApi],
  setup(props, ctx) {
    const { handleFetchApi } = useInitApi(props);
    const state = useInitData(props);
    const iModelValue = reactive({});
    const calendarData = ref('');

    const fetchDataList = () => {
      const apiData = props.initApi;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          calendarData.value = result;
        });
    };

    watch(() => props.modelValue, (val) => {
      iModelValue[props.name] = val || new Date().toISOString();
    }, {
      immediate: true,
    });

    watch(() => iModelValue[props.name], (val, old) => {
      ctx.emit('update:modelValue', val);
      if (props.target) {
        props.linkageTrigger(props.target, iModelValue);
      }
    });

    onMounted(() => {
      if (props.initApi) {
        fetchDataList();
      }
    });

    return {
      ...useLinkage(props),
      ...useDerivedProps(props),
      calendarData,
      iModelValue,
      state,
    };
  }
});
</script>
