<template>
  <el-form
    ref="form"
    v-loading="loading"
    class="umis-form__container"
    :label-width="labelWidth"
    :label-position="labelPosition"
    :model="state.data"
    :inline="inline"
  >
    <template v-if="!fieldset">
      <template
        v-for="(item, index) in inactiveControls"
        :key="`${path}/controls/${index}/${item.renderer}`"
      >
        <mis-component
          v-if="!$formItems.includes(item.renderer)"
          :ref="item.actionType"
          :key="`${path}/controls/${index}/${item.renderer}`"
          :path="`${path}/controls/${index}`"
          :track="`${path}/controls/${index}`"
          :mis-name="item.renderer"
          :header="iHeader(item)"
          :body="iBody(item)"
          :footer="iFooter(item)"
          :props="iAttrs(item)"
          :init-data="state.data"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :disabled-on="item.disabledOn"
        />
        <mis-field
          v-else
          :key="`${path}/controls/${index}/${item.renderer}`"
          v-model="state.data[item.name]"
          :path="`${path}/controls/${index}`"
          :track="`${path}/controls/${index}`"
          :name="item.name"
          :props="{...item, loading}"
          :init-data="state.data"
          :hidden-on="item.hiddenOn"
          :visible-on="item.visibleOn"
          :disabled-on="item.disabledOn"
          :field-changed="onFieldChanged"
          :handle-invisible="handleInvisible"
          :action="formActionMap(item)"
        />
      </template>
    </template>
    <template v-else>
      <el-collapse v-model="activeCollapse">
        <fieldset
          v-for="(control, index) in controls"
          :key="`${path}/${index}`"
          :class="control.classname"
          class="umis-form__container__fieldset"
        >
          <el-collapse-item :name="index">
            <template #title>
              <legend>
                <span class="umis-form__container__legend">
                  {{ control.title }}
                </span>
              </legend>
            </template>
            <div class="umis-form__container__fieldset__items">
              <template
                v-for="(item, jndex) in control.controls"
                :key="`${path}/${jndex}/${item.renderer}`"
              >
                <mis-field
                  v-if="$formItems.includes(item.renderer)"
                  v-model="state.data[item.name]"
                  :path="`${path}/${jndex}`"
                  :name="item.name"
                  :props="{...item, loading}"
                  :init-data="state.data"
                  :hidden-on="item.hiddenOn"
                  :visible-on="item.visibleOn"
                  :disabled-on="item.disabledOn"
                  :field-changed="onFieldChanged"
                  :handle-invisible="handleInvisible"
                  :formitems="$formItems"
                  :action="formActionMap(item)"
                />
                <mis-component
                  v-else
                  :path="`${path}/${item.renderer}`"
                  :mis-name="item.renderer"
                  :header="iHeader(item)"
                  :body="iBody(item)"
                  :footer="iFooter(item)"
                  :props="iAttrs(item)"
                  :init-data="state.data"
                  :action="formActionMap(item)"
                />
              </template>
            </div>
            <div
              v-if="control.viewer"
              class="umis-form__container__viewer"
            >
              <mis-component
                :path="`${path}/${control.viewer.renderer}`"
                :mis-name="control.viewer.renderer"
                :header="iHeader(control.viewer)"
                :body="iBody(control.viewer)"
                :footer="iFooter(control.viewer)"
                :props="iAttrs(control.viewer)"
                :init-data="state.data"
              />
            </div>
          </el-collapse-item>
        </fieldset>
      </el-collapse>
    </template>
    <el-form-item
      v-if="!fieldset"
      class="umis-form__actions__wrapper"
    >
      <template #label>
&nbsp;
      </template>
      <mis-component
        v-for="(item, index) in activeControls"
        :key="`${path}/controls/${inactiveControls.length+index}/${item.renderer}`"
        :track="`${path}/controls/${inactiveControls.length+index}/${item.renderer}`"
        :path="`${path}/controls/${inactiveControls.length+index}/${item.renderer}`"
        :ref="item.actionType"
        :mis-name="item.renderer"
        :header="iHeader(item)"
        :body="iBody(item)"
        :footer="iFooter(item)"
        :props="iAttrs(item)"
        :init-data="{
          ...state.data,
          submitLocked
        }"
        :hidden-on="item.hiddenOn"
        :visible-on="item.visibleOn"
        :disabled-on="item.disabledOn"
        :action="formActionMap(item)"
      />
    </el-form-item>
  </el-form>
</template>
<script>
import deepEqual from 'deep-equal';
import clonedeep from 'lodash.clonedeep';
import debounce from 'lodash.debounce';
import {defineComponent, onMounted, computed, getCurrentInstance, onBeforeUnmount, ref} from 'vue';
import { ElForm } from 'element-plus';
import useInitApi from '../../../hooks/useInitApi';
import useDerivedProps from '../../../hooks/useDerivedProps';
import useInitData from '../../../hooks/useInitData';
import useLinkage from '../../../hooks/useLinkage';
import initApi from '../../props/init-api';
import initData from '../../props/init-data';
import mixinProps from '../../props/form/form';

export default defineComponent({
  name: 'MisForm',
  components: {
    ElForm,
  },
  mixins: [mixinProps, initApi, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi } = useInitApi(props);
    const { onLinkageTrigger } = useLinkage(props);
    const state = useInitData(props);
    const activeCollapse = [];
    const data = {};
    const loading = ref(false);
    const submitLocked = ref(false);
    let cachedData;
    for (let i = 0; i < props.controls.length; i++) {
      let controls;
      if (props.fieldset && props.controls[i].renderer !== 'mis-combo') {
        /* eslint-disable */
        controls = props.controls[i].controls;
      } else {
        controls = [props.controls[i]];
      }
      activeCollapse.push(i);
      controls.forEach((control) => {
        const { renderer } = control;
        const { name } = control;
        let value = state.data[name] || control.defaultValue;
        if (renderer === 'mis-calendar' && !value) {
          value = new Date();
        }
        if (name && renderer !== 'mis-action') {
          if (typeof value === 'undefined') {
            value = '';
          }
          data[name] = value;
        }
        if (control.rules) {
          control.rules.forEach(item => {
            const exp = item.exp;
            if (exp) {
              item.validator = (rule, value, callback) => {
                if (Object.prototype.toString.call(exp) === '[object String]') {
                  const isValid = proxy.$expressionEval(exp, state.data);
                  if (isValid) {
                    callback(new Error(item.message));
                  } else {
                    callback();
                  }
                } else {
                  handleFetchApi(exp, state.data).then((result) => {
                    if (result.isValid) {
                      callback();
                    } else {
                      callback(result.message);
                    }
                  })
                }
              }
            }
          });
        }
      });
    }
    Object.assign(state.data, data);
    let invisibleField = [];
    const activeControls = computed(() => {
      if (!props.fieldset) {
        return props.controls.filter((item) => {
          if (item.renderer === 'mis-action') {
            if (item.actionType === 'mis-submit') {
              item.actionApi = props.initApi;
            }
            return item;
          }
        });
      }
      let activeControls = [];
      for (const key in props.controls) {
        if (Object.hasOwnProperty.call(props.controls, key)) {
          const active = props.controls[key].controls.filter((item) => {
            if (item.renderer === 'mis-action') {
              if (item.actionType === 'mis-submit') {
                item.actionApi = props.initApi;
              }
              return item;
            }
          });
          activeControls = activeControls.concat(active);
        }
      }
      return activeControls;
    });
    const inactiveControls = computed(() => {
      if (!props.fieldset) {
        return props.controls.filter((item) => item.renderer !== 'mis-action');
      }
      return [];
    });

    const onComponentUpdate = () => {
      if (props.initApi && props.initApi.url) {
        fetchDataList();
      }
    };
    const fetchDataList = () => {
      const apiData = clonedeep(props.initApi);
      loading.value = true;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          Object.assign(state.data, result);
        })
        .catch((err) => {
          console.error(err);
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const handleFieldUpdate = (name, value) => {
      state.data[name] = value;
    };
    const handleInvisible = (visible, field) => {
      if (visible) {
        invisibleField = invisibleField.filter(
          (item) => item !== field,
        );
      } else {
        invisibleField.push(field);
        handleResetField(field);
      }
    };
    const handleResetField = (field) => {
      const type = Object.prototype.toString.call(state.data[field]);
      switch (type) {
        case '[object String]': state.data[field] = ''; break;
        case '[object Number]': state.data[field] = 0; break;
        case '[object Boolean]': state.data[field] = false; break;
        case '[object Array]': state.data[field] = []; break;
        case '[object Object]': state.data[field] = {}; break;
        default: state.data[field] = ''; break;
      }
    };
    const handleRemoteSubmit = (actionType, target, feedback) => {
      if (props.name && props.name === target) {
        const { form } = proxy.$.refs;
        if (form && actionType === 'mis-reset') {
          form.resetFields();
        } else if (form && actionType === 'mis-submit') {
          form.validate((valid) => {
            if (valid) {
              sendFormData(feedback);
            }
          });
        }
      }
    };
    const handleItemFeedback = (data) => {
      Object.assign(state.data, data);
    };
    const onFieldChanged = debounce(() => {
      if (props.idem && submitLocked.value) {
        const isChanged = !deepEqual(state.data, cachedData);
        if (isChanged) {
          submitLocked.value = false;
        }
      }
    });
    const formActionMap = (item) => {
      if (item.renderer === 'mis-upload') {
        return handleItemFeedback;
      }
      if (item.actionType === 'mis-submit') {
        return onBeforeSubmit;
      }
      if (item.actionType === 'mis-reset') {
        return resetForm;
      }
    };
    const resetForm = () => {
      const { form } = proxy.$.refs;
      form.resetFields();
    };

    const onBeforeSubmit = () => {
      const { form } = proxy.$.refs;
      form.validate((valid) => {
        if (valid) {
          sendFormData();
        }
      });
    };
    const sendFormData = (feedback) => {
      const formData = {};
      for (const name in state.data) {
        if (
          Object.hasOwnProperty.call(state.data, name)
          && !invisibleField.includes(name)
        ) {
          formData[name] = state.data[name];
        }
      }
      if (props.target) {
        formData.timestamp = new Date().getTime();
        return onLinkageTrigger(props.target, formData);
      }
      loading.value = true;
      handleFetchApi(
        {
          url: props.actionApi.url,
          method: props.actionApi.method || 'post',
          params: formData,
        },
        formData,
      ).then((res) => {
        Object.assign(state.data, res);
        if (props.idem) {
          submitLocked.value = true;
          cachedData = clonedeep(state.data);
        }
        feedback && feedback(res);
        onAfterSubmit(res);
      })
      .catch(err => {
        console.log(props.name, err);
      })
      .finally(() => {
        loading.value = false;
      });
    };
    const onAfterSubmit = (res) => {
      if (props.redirect) {
        proxy.$parent.filterAction({actionType: 'mis-url', url: props.redirect}, state.data);
      } else if (props.target && props.linkageType === 'after') {
        return onLinkageTrigger(props.target, res);
      } else if (props.reload) {
        proxy.$eventHub.$off('mis-component:remoteComponent', handleRemoteSubmit);
        proxy.$eventHub.$emit('mis-component:reload', props.reload);
      }
    };

    onMounted(() => {
      onComponentUpdate();
      proxy.$eventHub.$on('mis-component:remoteComponent', handleRemoteSubmit);
    });
    onBeforeUnmount(() => {
      proxy.$eventHub.$off('mis-component:remoteComponent', handleRemoteSubmit);
    });

    return {
      ...useDerivedProps(props),
      ...useLinkage(props),
      onComponentUpdate,
      handleFieldUpdate,
      handleInvisible,
      onFieldChanged,
      onBeforeSubmit,
      handleItemFeedback,
      formActionMap,
      activeCollapse,
      invisibleField,
      activeControls,
      inactiveControls,
      submitLocked,
      loading,
      state,
    };
  },
});
</script>
