<template>
  <div
    v-if="iVisible"
    v-show="iHidden"
    class="umis-crud__container"
  >
    <div
      v-if="showDynamicColumn || showExport || actions || title"
      class="umis-crud__container__header"
      :class="{ border: border }"
    >
      <div class="umis-crud__container__title">
        {{ title }}
      </div>
      <div class="umis-crud__container__tools">
        <template
          v-for="(item, index) in actions"
          :key="`${path}/actions/${index}`"
        >
          <mis-component
            :path="`${path}/actions/${index}/${item.renderer}`"
            :mis-name="item.renderer"
            :props="iAttrs(item)"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
            :init-data="{selectedId: selectedId}"
            :action="action"
          />
        </template>
        <el-button
          v-if="showExport"
          class="umis-component__fragment"
          size="mini"
          icon="el-icon-download"
          @click="onExportExcel"
        >
          导出
        </el-button>
        <el-dropdown
          v-if="showDynamicColumn"
          class="umis-component__fragment"
          size="mini"
          trigger="click"
          split-button
        >
          <i class="el-icon-s-grid" />
          <template #dropdown>
            <el-dropdown-menu>
              <el-checkbox-group v-model="dynamicColumn">
                <template
                  v-for="(column, index) in columns"
                  :key="index"
                >
                  <el-dropdown-item v-if="column.name">
                    <el-checkbox :label="column.name">
                      {{ column.label }}
                    </el-checkbox>
                  </el-dropdown-item>
                </template>
              </el-checkbox-group>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>
    <el-table
      ref="componentDom"
      v-loading="loading"
      :data="rows"
      :height="height"
      :stripe="stripe"
      :border="border"
      :fit="fit"
      :max-height="maxHeight"
      :show-header="showHeader"
      :span-method="onMergeCell"
      @selection-change="onSelectionChange"
      @sort-change="onSortChange"
    >
      <template
        v-for="(column, index) in columns"
        :key="`${path}/columns/${index}`"
      >
        <template v-if="dynamicColumn.includes(column.name)">
          <el-table-column
            v-if="column.body || column.header"
            :prop="column.name"
            :label="column.label"
            :fixed="column.fixed"
            :align="column.align"
            :width="column.width"
            :type="column.type"
            :sortable="column.sortable"
          >
            <template
              v-if="column.header"
              #header
            >
              <mis-component
                :path="`${path}/head/${index}/${column.header.renderer}`"
                :mis-name="column.header.renderer"
                :header="iHeader(column.header)"
                :body="iBody(column.header)"
                :footer="iFooter(column.header)"
                :props="iAttrs(column.header)"
              />
            </template>
            <template #default="scope">
              <template v-if="scope.$index >= 0">
                <template v-if="column.body && column.body.length && Object.prototype.toString.call(column.body) === '[object Array]'">
                  <mis-component
                    v-for="(item, count) in column.body"
                    :key="`${path}/rows/${scope.$index}/columns/${index}/body/${count}`"
                    :path="`${path}/rows/${scope.$index}/columns/${index}/body/${count}`"
                    :track="`${path}/columns/${index}/body/${count}`"
                    :mis-name="item.renderer"
                    :header="iHeader(item)"
                    :body="iBody(item)"
                    :footer="iFooter(item)"
                    :init-data="scope.row"
                    :props="iAttrs(item)"
                  />
                </template>
                <mis-component
                  v-else-if="column.body && Object.prototype.toString.call(column.body) === '[object Object]'"
                  :path="`${path}/rows/${scope.$index}/columns/${index}`"
                  :mis-name="column.body.renderer"
                  :header="iHeader(column.body)"
                  :body="iBody(column.body)"
                  :footer="iFooter(column.body)"
                  :init-data="scope.row"
                  :props="iAttrs(column.body)"
                />
                <template v-else>
                  {{scope.row[scope.column.property]}}
                </template>
              </template>
            </template>
          </el-table-column>
          <el-table-column
            v-else
            :prop="column.name"
            :label="column.label"
            :fixed="column.fixed"
            :type="column.type"
            :align="column.align"
            :width="column.width"
            :sortable="column.sortable"
          />
        </template>
      </template>
    </el-table>
    <el-pagination
      v-if="hasPageInfo && rows && rows.length"
      class="umis-crud__container__pagination"
      background
      hide-on-single-page
      layout="prev, pager, next, sizes, jumper"
      :total="pageInfo.total"
      :page-size="pageInfo.pageSize"
      :current-page="pageInfo.pageIndex"
      :page-sizes="[10, 20, 30, 40, 50, 100]"
      @current-change="onPageIndexChange"
      @size-change="onPageSizeChange"
    />
  </div>
</template>
<script>
import {defineComponent, getCurrentInstance, onMounted, ref, watch} from 'vue';
import clonedeep from 'lodash.clonedeep';

import {
  ElTable,
  ElTableColumn,
  ElPagination,
  ElDropdown,
  ElDropdownMenu,
  ElDropdownItem,
  ElCheckboxGroup,
  ElCheckbox,
} from 'element-plus';

import useLinkage from '../../hooks/useLinkage';
import useDerivedProps from '../../hooks/useDerivedProps';
import usePageInfo from '../../hooks/usePageInfo';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/table';

export default defineComponent({
  name: 'MisTable',
  components: {
    ElTable,
    ElTableColumn,
    ElPagination,
    ElDropdown,
    ElDropdownMenu,
    ElDropdownItem,
    ElCheckboxGroup,
    ElCheckbox,
  },
  mixins: [mixinProps, visible, initApi, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const pageInfo = usePageInfo(props);
    const iColumn = props.columns.map((item) => item.name);
    const selectedId = ref([]);
    const rows = ref([]);
    const dynamicColumn = ref(iColumn);
    const loading = ref(false);
    const sortable = {};
    const onSelectionChange = (val) => {
      selectedId.value = val;
    };
    const onSortChange = ({prop, order}) => {
      sortable.orderBy = prop;
      sortable.orderDir = order;
      pageInfo.pageIndex = 1;
      fetchDataList();
    };
    const onExportExcel = async() => {
      try {
        const ExportJsonExcel = await import('js-export-excel');
        const options = {
          name: props.title || 'sheetjs',
          datas: [{
            sheetData: rows.value,
            sheetName: 'sheet',
            sheetHeader: props.columns.map(column => column.label)
          }]
        };
        new ExportJsonExcel.default(options).saveExcel();
      } catch(e) {
        proxy.$message.error(e.message);
        console.error(e);
      }
    };
    const onPageSizeChange = (pageSize) => {
      pageInfo.pageSize = Number(pageSize);
      pageInfo.pageIndex = 1;
      fetchDataList();
    };
    const onPageIndexChange = (pageIndex) => {
      if (event.currentTarget.classList.value === 'el-pager') {
        pageInfo.pageIndex = Number(pageIndex);
      }
      fetchDataList();
    };
    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      }
    };
    const fetchDataList = () => {
      if (proxy && proxy.axiosCancelToken) {
        typeof proxy.axiosCancelToken === 'function' && proxy.axiosCancelToken('CANCELED_DUE_TO_NEW_REQUEST');
      }
      const apiData = clonedeep(props.initApi);
      loading.value = true;
      if (props.hasPageInfo && !apiData.params) {
        apiData.params = {};
      }
      if (sortable.orderBy) {
        apiData.params.orderBy = sortable.orderBy;
        apiData.params.orderDir = sortable.orderDir;
      }
      apiData.params[props.pageConfig.pageIndex] = pageInfo.pageIndex;
      apiData.params[props.pageConfig.pageSize] = pageInfo.pageSize;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          rows.value = result[props.pageConfig.rows];
          pageInfo.total = result[props.pageConfig.total];
        })
        .catch((err) => {
          if (err.message === 'CANCELED_DUE_TO_NEW_REQUEST') {
            console.info(err);
          }
        })
        .finally(() => {
          loading.value = false;
        });
    };
    const onMergeCell = (context) => {
      if (props.mergeCell) {
        for (const exp in props.mergeCell) {
          if (Object.hasOwnProperty.call(props.mergeCell, exp)) {
            if (proxy.$expressionEval(exp, context)) {
              return props.mergeCell[exp];
            }
          }
        }
      }
    };

    watch(() => props.initRows, (val) => {
      if (val && val.length) {
        rows.value = val;
      }
    }, {
      immediate: true,
    });

    onMounted(() => {
      onComponentUpdate();
    });

    return {
      ...useLinkage(props),
      ...useDerivedProps(props),
      ...useVisible(props),
      shouldComponentUpdate,
      onComponentUpdate,
      onSelectionChange,
      onExportExcel,
      onSortChange,
      onPageIndexChange,
      onPageSizeChange,
      onMergeCell,
      dynamicColumn,
      selectedId,
      pageInfo,
      loading,
      state,
      rows
    };
  },
});
</script>
