<template>
  <span class="umis-component__fragment">
    <el-dropdown
    >
      <el-button
        :size="size"
        :type="type"
        :plain="plain"
        :round="round"
        :circle="circle"
      >
        {{ text }}
        <i class="el-icon-arrow-down el-icon--right" />
      </el-button>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item
            v-for="(item, index) in body"
            :key="index"
            :command="index"
            :disabled="item.disabled"
            :divided="item.divided"
            :icon="item.icon"
          >
            <mis-component
              v-if="actionItems.includes(item.actionType)"
              :path="`/${path}/${index}/${item.renderer}`"
              :mis-name="item.renderer"
              :props="iAttrs(item)"
              :init-data="state.data"
              classname="umis-dropdown__item"
            />
          </el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </span>
</template>

<script>
import { defineComponent } from 'vue';
import { ElDropdown, ElDropdownMenu, ElDropdownItem } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/dropdown';

const actionItems = ['mis-ajax', 'mis-copy', 'mis-redirect', 'mis-url'];

export default defineComponent({
  name: 'MisDropdown',
  components: {
    ElDropdown,
    ElDropdownMenu,
    ElDropdownItem,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      actionItems,
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
