<template>
  <el-carousel
    :initial-index="initialIndex"
    :trigger="trigger"
    :autoplay="autoplay"
    :interval="interval"
    :indicator-position="indicatorPosition"
    :arrow="arrow"
    :type="type"
    :loop="loop"
    :direction="direction"
    class="umis-carousel__container"
  >
    <el-carousel-item
      v-for="(item, index) in body"
      :key="index"
    >
      <mis-component
        :mis-name="item.renderer"
        :path="`${path}/body/${index}/${item.renderer}`"
        :body="iBody(item)"
        :header="iHeader(item)"
        :footer="iFooter(item)"
        :props="iAttrs(item)"
        :init-data="state.data"
      />
    </el-carousel-item>
  </el-carousel>
</template>

<script>
import { defineComponent } from 'vue';
import { ElCarousel, ElCarouselItem } from 'element-plus';

import useInitData from '../../hooks/useInitData';
import useDerivedProps from '../../hooks/useDerivedProps';
import initData from '../props/init-data';
import mixinProps from '../props/carousel';

export default defineComponent({
  name: 'MisCarousel',
  components: {
    ElCarousel,
    ElCarouselItem,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);

    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
