<template>
  <span
    v-if="inline"
    :class="classname"
    class="umis-html__container"
    v-html="getHtml"
  />
  <div
    v-else
    :class="classname"
    class="umis-html__container"
    v-html="getHtml"
  />
</template>
<script>
import { defineComponent, computed, getCurrentInstance } from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/html';

export default defineComponent({
  name: 'MisHtml',
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const getHtml = computed(() => proxy.$renderTpl(props.html, props.initData));

    return {
      ...useDerivedProps(),
      state,
      getHtml,
    };
  },
});
</script>
