<template>
  <div class="umis-schema__container">
    <template v-if="!iSchemaLoading">
      <template v-if="Object.prototype.toString.call(iSchema) === '[object Array]'">
        <template
          v-for="(item, index) in iSchema"
          :key="`/${item.renderer}/${index}`"
        >
          <mis-component
            v-bind="iAttrs(item)"
            :mis-name="item.renderer"
            :path="`/${item.renderer}/${index}`"
            :track="`/${item.renderer}/${index}`"
            :track-id="index"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
          />
        </template>
      </template>
      <mis-component
        v-else-if="iSchema && iSchema.renderer"
        v-bind="iAttrs(iSchema)"
        :path="`/${iSchema.renderer}`"
        :track="`/${iSchema.renderer}`"
        :mis-name="iSchema.renderer"
        :header="iHeader(iSchema)"
        :body="iBody(iSchema)"
        :footer="iFooter(iSchema)"
      />
    </template>
    <el-skeleton
      v-else
      :rows="10"
      animated
    >
      <template #template>
        <el-skeleton-item
          variant="image"
          style="width: 240px; height: 240px; margin-bottom: 10px;"
        />
        <el-skeleton-item
          variant="p"
          style="width: 20%"
        />
        <el-skeleton-item
          variant="p"
          style="width: 40%"
        />
        <el-skeleton-item
          variant="p"
          style="width: 60%"
        />
        <div class="umis-schema__container__skeleton">
          <el-skeleton-item
            variant="text"
            style="margin-right: 16px;"
          />
          <el-skeleton-item
            variant="text"
            style="width: 30%;"
          />
        </div>
      </template>
    </el-skeleton>
  </div>
</template>
<script>
import {
  defineComponent, getCurrentInstance, onMounted, watch, computed, reactive, ref, nextTick
} from 'vue';
import clonedeep from 'lodash.clonedeep';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import propsMixin from '../props/schema';

const defaultPage = {
  renderer: 'mis-page',
  body: []
};

export default defineComponent({
  name: 'MisSchema',
  mixins: [propsMixin, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const iSchemaLoading = ref(false);
    const iStopAutoRefresh = ref(false);
    const isMounted = ref(false);
    const iSchema = ref(props.initSchema);

    const getPageSchema = () => {
      iSchemaLoading.value = true;
      proxy.$api
        .staticApi(proxy.$umisConfig).get(props.url)
        .then((res) => {
          const { pageSchema, ...pageInfo } = res.data;
          let schema = pageSchema;
          if (typeof pageSchema === 'string') {
            schema = JSON.parse(pageSchema) || defaultPage;
          }
          iSchema.value = schema;
          iSchemaLoading.value = false;
          if (props.canSchemaUpdate) {
            window.UMIS = { pageInfo, pageSchema: clonedeep(schema) };
          }
        }).catch((err) => {
          console.log(err);
        });
    };

    const updatePageSchema = (schema) => {
      if (props.canSchemaUpdate) {
        iSchemaLoading.value = true;
        const timer = setTimeout(() => {
          iSchema.value = schema;
          iSchemaLoading.value = false;
          window.UMIS.pageSchema = clonedeep(schema);
          clearTimeout(timer);
        }, 200);
      }
    };

    const path = computed(() => {
      if (!props.iProtal) {
        return '';
      }
      return props.canSchemaUpdate ? proxy.$router.currentRoute.value.path : '/website';
    });

    watch(() => props.url, (val, old) => {
      if (val && val !== old) {
        if (!props.initSchema) {
          getPageSchema();
        } else {
          iSchemaLoading.value = true;
          nextTick(() => {
            iSchemaLoading.value = false;
            iSchema.value = clonedeep(props.initSchema);
          });
        }
      }
    });

    onMounted(() => {
      isMounted.value = true;
      proxy.$eventHub.$on('mis-schema:change', updatePageSchema);
      if (props.url && !props.initSchema) {
        getPageSchema();
      }
      if (!props.canSchemaUpdate) {
        // proxy.$initSetting(proxy, proxy.$umisConfig);
      }
    });

    return {
      ...useDerivedProps(props),
      ...useInitData(props),
      path,
      iSchema,
      iSchemaLoading,
      iStopAutoRefresh,
      isMounted,
    };
  },
});
</script>
