<template>
  <el-main
    v-if="iVisible"
    v-show="iHidden"
    :class="[classname, iComputedClass]"
    class="umis-main__container"
  >
    <router-view
      v-if="routerView"
      :init-data="state.data"
    />
    <template v-else>
      <template
        v-if="Object.prototype.toString.call(body) === '[object Array]'"
      >
        <template
          v-for="(child, index) in body"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            v-bind="iAttrs(child)"
            :mis-name="child.renderer"
            :path="`${path}/body/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
        v-bind="iAttrs(body)"
        :mis-name="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :track="`${path}/body/${body.renderer}`"
        :track-id="body.renderer"
        :header="iHeader(body)"
        :body="iBody(body)"
        :footer="iFooter(body)"
        :props="iAttrs(body)"
        :init-data="state.data"
      />
    </template>
  </el-main>
</template>

<script>
import {
  defineComponent, computed, getCurrentInstance,
} from 'vue';
import { ElMain } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/main';

export default defineComponent({
  name: 'MisMain',
  components: {
    ElMain,
  },
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);

    const iComputedClass = computed(() => {
      if (props.computedClass) {
        return proxy.$expressionEval(props.computedClass, state.data);
      }
      return '';
    });

    return {
      ...useDerivedProps(props),
      ...useVisible(props),
      iComputedClass,
      state,
    };
  },
});
</script>
