<template>
  <el-row
    :gutter="gutter"
    :type="type"
    :justify="justify"
    :align="align"
    :tag="tag"
    :style="style"
    class="mis-grid__container"
  >
    <el-col
      v-for="(child, index) in body"
      :key="`${path}/${index}/${child.renderer}`"
      :class="classname"
      :span="child.span || span"
    >
      <mis-component
        v-bind="iAttrs(child)"
        :mis-name="child.renderer"
        :path="`${path}/body/${index}/${child.renderer}`"
        :header="iHeader(child)"
        :body="iBody(child)"
        :footer="iFooter(child)"
        :props="iAttrs(child)"
        :init-data="state.data"
      />
    </el-col>
  </el-row>
</template>
<script>
import { defineComponent } from 'vue';
import { ElRow, ElCol } from 'element-plus';

import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/grid';

export default defineComponent({
  name: 'MisGrid',
  components: {
    ElRow,
    ElCol,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const state = useInitData(props);
    return {
      ...useDerivedProps(props),
      state,
    };
  },
});
</script>
