<template>
  <el-divider
    :direction="direction"
    :content-position="contentPosition"
  >
    <i
      v-if="icon"
      :class="icon"
    />
    <template v-else>
      {{ renderTitle }}
    </template>
  </el-divider>
</template>

<script>
import { defineComponent, computed, getCurrentInstance } from 'vue';
import { ElDivider } from 'element-plus';

import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import mixinProps from '../props/divider';

export default defineComponent({
  name: 'MisDivider',
  components: {
    ElDivider,
  },
  mixins: [mixinProps, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const renderTitle = computed(() => proxy.$renderTpl(props.text, props.initData));

    return {
      state,
      renderTitle,
    };
  },
});
</script>
