<template>
  <el-card
    class="umis-setting__card-margin"
    shadow="hover"
  >
    <template #header>
      <div
        class="umis-setting__header"
      >
        <el-tooltip
          content="选中的域名将作为接口请求的路径前缀"
          placement="top-start"
        >
          <span>域名选择器 <i class="el-icon-info" /> </span>
        </el-tooltip>
        <el-button-group>
          <el-button
            plain
            size="mini"
            type="primary"
            @click="onAdd"
          >
            添加
          </el-button>
          <el-button
            plain
            size="mini"
            type="primary"
            @click="onSave"
          >
            应用
          </el-button>
        </el-button-group>
      </div>
    </template>
    <el-radio-group
      v-model="activeDomain"
      class="umis-setting__api-editor"
    >
      <div
        v-for="(domain, index) in domains"
        :key="index"
        class="umis-setting__api-editor__item"
      >
        <span
          class="umis-setting__api-editor__delete"
          @click="onDel(index)"
        >
          <i class="el-icon-delete" />
        </span>
        <el-input
          v-model="domain.key"
          class="umis-setting__api-editor__label"
        />
        <el-input
          v-model="domain.value"
          class="umis-setting__api-editor__value"
          placeholder="请输入域名"
        />
        <el-radio
          :label="domain.key"
          class="umis-setting__api-editor__radio"
        >
          {{ '' }}
        </el-radio>
      </div>
    </el-radio-group>
  </el-card>
</template>
<script>
import { getCurrentInstance, defineComponent, ref, onMounted } from 'vue';
import {
  ElCard,
  ElRadioGroup,
  ElRadio,
  ElButtonGroup,
  ElButton,
  ElInput,
  ElTooltip,
} from 'element-plus';

export default defineComponent({
  name: 'MisDomain',
  comments: {
    ElCard,
    ElRadioGroup,
    ElRadio,
    ElButtonGroup,
    ElButton,
    ElInput,
    ElTooltip,
  },
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const activeDomain = ref('');
    const domains = ref([]);
    const onAdd = () => {
      domains.value.push({
        key: `UMIS_DOMAIN_DEV_${domains.value.length}`,
        value: ''
      });
    };
    const onDel = (index) => {
      domains.value.splice(index, 1);
    };
    const onSave = () => {
      const domainMap = {};
      let active = '';
      domains.value.forEach(item => {
        if (activeDomain.value === item.key) {
          active = item.value;
        }
        domainMap[item.key] = item.value;
      });
      ctx.emit('input', domainMap);
      proxy.$umisConfig.isApiChanged = true;
      proxy.$umisConfig.activeDomain = active;
      proxy.$umisConfig.domains = domainMap;
      proxy.$message.success('配置已生效！');
    };
    onMounted(() => {
      activeDomain.value = proxy.$umisConfig.domains.default;
      proxy.$umisConfig.activeDomain = activeDomain.value;
      domains.value = Object.keys(proxy.$umisConfig.domains).map(key => {
        return {key, value: proxy.$umisConfig.domains[key]};
      });
    });

    return {
      onAdd,
      onDel,
      onSave,
      activeDomain,
      domains
    };
  }
});
</script>
