<template>
  <div
    v-loading="loading"
    class="umis-crud__container"
  >
    <el-row :gutter="gutter">
      <el-col
        v-for="(item, index) in rows"
        :key="`${path}/${index}`"
        :span="span"
      >
        <mis-card
          :path="`${path}/${index}`"
          :classname="classname"
          :shadow="shadow"
          :body-style="bodyStyle"
          :header="header"
          :body="body"
          :footer="footer"
          :actions="actions"
          :action="action"
          :init-data="item"
        />
      </el-col>
    </el-row>
    <el-affix
      v-if="!$umisConfig.editable"
      target=".umis-crud__container"
      position="bottom"
    >
      <el-pagination
        v-if="hasPageInfo && rows.length"
        class="umis-crud__container__pagination"
        background
        hide-on-single-page
        layout="prev, pager, next, sizes, jumper"
        :total="pageInfo.total"
        :page-size="pageInfo.pageSize"
        :current-page="pageInfo.pageIndex"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        @current-change="onPageIndexChange"
        @size-change="onPageSizeChange"
      />
    </el-affix>
  </div>
</template>
<script>
import {
  defineComponent, reactive, getCurrentInstance, onMounted, ref,
} from 'vue';
import clonedeep from 'lodash.clonedeep';
import {
  ElRow, ElCol, ElPagination, ElAffix
} from 'element-plus';
import useDerivedProps from '../../hooks/useDerivedProps';
import usePageInfo from '../../hooks/usePageInfo';
import useLinkage from '../../hooks/useLinkage';
import useInitApi from '../../hooks/useInitApi';
import useInitData from '../../hooks/useInitData';
import initData from '../props/init-data';
import initApi from '../props/init-api';
import mixinProps from '../props/cards';
import MisCard from './card.vue';

export default defineComponent({
  name: 'MisCards',
  components: {
    MisCard,
    ElRow,
    ElCol,
    ElPagination,
    ElAffix
  },
  mixins: [mixinProps, initData, initApi],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { handleFetchApi, shouldComponentUpdate } = useInitApi(props);
    const state = useInitData(props);
    const pageInfo = usePageInfo(props);
    const rows = ref([]);
    const loading = ref(false);
    const iBody = reactive(props.body);
    const renderHeader = (data) => proxy.$getRenderedTpl(props.header, state.data);

    const onPageSizeChange = (pageSize) => {
      pageInfo.pageSize = Number(pageSize);
      pageInfo.pageIndex = 1;
      fetchDataList();
    };
    const onPageIndexChange = (pageIndex) => {
      if (event.currentTarget.classList.value === 'el-pager') {
        pageInfo.pageIndex = Number(pageIndex);
      }
      fetchDataList();
    };

    const onComponentUpdate = () => {
      if (props.initApi) {
        fetchDataList();
      }
    };

    const fetchDataList = () => {
      const apiData = clonedeep(props.initApi);
      loading.value = true;
      if (!apiData.params) {
        apiData.params = {};
      }
      apiData.params[props.pageConfig.pageIndex] = pageInfo.pageIndex;
      apiData.params[props.pageConfig.pageSize] = pageInfo.pageSize;
      handleFetchApi(apiData, state.data)
        .then((result) => {
          rows.value = result[props.pageConfig.rows];
          pageInfo.total = result[props.pageConfig.total];
        })
        .finally(() => {
          loading.value = false;
        });
    };

    onMounted(() => {
      onComponentUpdate();
    });

    return {
      ...useDerivedProps(props),
      ...useLinkage(props),
      shouldComponentUpdate,
      onComponentUpdate,
      onPageSizeChange,
      onPageIndexChange,
      renderHeader,
      iBody,
      pageInfo,
      loading,
      state,
      rows
    };
  },
});
</script>
