<template>
  <div
    v-if="iVisible"
    v-show="iHidden"
    :style="style"
    :class="['umis-wrapper__container', `index-${index}`, classname, iComputedClass]"
  >
    <div
        v-if="header"
        class="umis-wrapper__container__header"
    >
      <template v-if="Object.prototype.toString.call(header) === '[object Array]'">
        <template
          v-for="(child, index) in header"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            :mis-name="child.renderer"
            :path="`${path}/header/${index}/${child.renderer}`"
            :props="iAttrs(child)"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(header) === '[object Object]'"
        :mis-name="header.renderer"
        :path="`${path}/header/${header.renderer}`"
        :body="iBody(header)"
        :header="iHeader(header)"
        :footer="iFooter(header)"
        :props="iAttrs(header)"
        :init-data="state.data"
      />
    </div>
    <div
      v-if="body"
      class="umis-wrapper__container__body"
    >
      <template v-if="Object.prototype.toString.call(body) === '[object Array]'">
        <template
          v-for="(item, index) in body"
          :key="`${path}/${index}/${item.renderer}`"
        >
          <mis-component
            v-bind=" iAttrs(item)"
            :mis-name="item.renderer"
            :path="`${path}/body/${index}/${item.renderer}`"
            :index="index"
            :hidden-on="item.hiddenOn"
            :visible-on="item.visibleOn"
            :header="iHeader(item)"
            :body="iBody(item)"
            :footer="iFooter(item)"
            :props="iAttrs(item)"
            :init-data="iData(item)"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(body) === '[object Object]'"
        v-bind=" iAttrs(body)"
        :mis-name="body.renderer"
        :path="`${path}/body/${body.renderer}`"
        :hidden-on="body.hiddenOn"
        :visible-on="body.visibleOn"
        :body="iBody(body)"
        :header="iHeader(body)"
        :footer="iFooter(body)"
        :props=" iAttrs(body)"
        :init-data="iData(body)"
      />
    </div>
    <div
      v-if="footer"
      class="umis-wrapper__container__footer"
    >
      <template v-if="Object.prototype.toString.call(footer) === '[object Array]'">
        <template
          v-for="(child, index) in footer"
          :key="`${path}/${index}/${child.renderer}`"
        >
          <mis-component
            :mis-name="child.renderer"
            :path="`${path}/footer/${index}/${child.renderer}`"
            :header="iHeader(child)"
            :body="iBody(child)"
            :footer="iFooter(child)"
            :props="iAttrs(child)"
            :init-data="state.data"
          />
        </template>
      </template>
      <mis-component
        v-else-if="Object.prototype.toString.call(footer) === '[object Object]'"
        :mis-name="footer.renderer"
        :path="`${path}/footer/${footer.renderer}`"
        :header="iHeader(footer)"
        :body="iBody(footer)"
        :footer="iFooter(footer)"
        :props="iAttrs(footer)"
        :init-data="state.data"
      />
    </div>
  </div>
</template>
<script>
import { defineComponent, computed, getCurrentInstance, onMounted } from 'vue';
import useDerivedProps from '../../hooks/useDerivedProps';
import useInitData from '../../hooks/useInitData';
import useVisible from '../../hooks/useVisible';
import useLinkage from '../../hooks/useLinkage';
import visible from '../props/visible';
import initData from '../props/init-data';
import mixinProps from '../props/wrapper';
import {getUrlParams} from '../../utils/url';

export default defineComponent({
  name: 'MisWrapper',
  mixins: [mixinProps, visible, initData],
  setup(props) {
    const { proxy } = getCurrentInstance();
    const state = useInitData(props);
    const iComputedClass = computed(() => {
      if (props.computedClass) {
        return proxy.$expressionEval(props.computedClass, state.data);
      }
      return '';
    });

    onMounted(() => {
      if (props.mergeUrlQuery) {
        const defaultQuery = getUrlParams();
        Object.assign(state.data, defaultQuery);
      }
    });

    return {
      ...useDerivedProps(props),
      ...useVisible(props),
      ...useLinkage(props),
      iComputedClass,
      state,
    };
  },
});
</script>
