import { getCurrentInstance, onBeforeUnmount } from 'vue';
import deepEqual from 'deep-equal';
import { constructUrl, getUrlParams } from '../utils/url';

export default function (props) {
  const { proxy } = getCurrentInstance();
  let compiledCacheUrl = null;
  let compiledCacheParams = null;
  let cacheData = null;
  if (!proxy.$.appContext.UMIS_CACHED_APIS) {
    proxy.$.appContext.UMIS_CACHED_APIS = {};
  }

  const iSendOn = () => {
    if (props.sendOn) {
      return proxy.$expressionEval(props.sendOn, proxy.state.data);
    }
    return true;
  };
  const onCompiledParams = (params, mergedData) => {
    const compiledParams = proxy.$compiledParams(params, mergedData);
    compiledCacheParams = compiledParams;
    return compiledParams;
  };
  const onCompiledUrl = (url, mergedData) => {
    compiledCacheUrl = url;
    if (/\$\{.+?\}/g.test(url)) {
      compiledCacheUrl = proxy.$compiledUrl(url, mergedData);
    }
    return compiledCacheUrl;
  };
  const useSyncLocation = (params) => {
    const defaultQuery = {};
    for (const key in params) {
      if (params[key] !== '' && Object.hasOwnProperty.call(params, key)) {
        defaultQuery[key] = params[key];
      }
    }
    const newUrl = constructUrl(
      '',
      location.pathname,
      defaultQuery,
      location.hash,
      location.protocol,
    );
    window.history.replaceState({}, window.document.title, newUrl);
  };
  const shouldComponentUpdate = (val) => {
    const mergedData = val;
    const { url, params } = props.initApi;
    const compiledUrl = proxy.$compiledUrl(url, mergedData);
    const compiledParams = proxy.$compiledParams(params, mergedData);

    if (
      (params === '*' && !deepEqual(cacheData, val))
      || (compiledCacheUrl !== compiledUrl)
      || (compiledCacheParams !== null && !deepEqual(compiledParams, compiledCacheParams))
    ) {
      cacheData = val;
      return true;
    }
  };
  const handleFetchApi = (apiConfig, context) => {
    if (!iSendOn()) {
      return Promise.resolve();
    }
    const {
      method = 'get', url, params, cached,
    } = apiConfig || props.initApi;
    const mergedData = context;
    let compiledParams = params;
    let compiledUrl = url;
    let fetchBody;
    let cancelToken;

    try {
      compiledParams = onCompiledParams(params, mergedData);
      compiledUrl = onCompiledUrl(url, mergedData);
    } catch (e) {
      proxy.$message.error(`动态参数错误：${e.message}`);
    }

    if (props.syncLocation) {
      useSyncLocation(compiledParams);
    }
    if (params === '*') {
      compiledParams = mergedData;
    }
    if (['get', 'delete'].includes(method)) {
      fetchBody = {
        params: compiledParams,
      };
      cancelToken = new proxy.$api.CancelToken(c => {
        proxy.axiosCancelToken = c;
      });
    } else {
      fetchBody = compiledParams;
      cancelToken = {cancelToken: new proxy.$api.CancelToken(c => {
        proxy.axiosCancelToken = c;
      })};
    }

    if (cached) {
      if (proxy.$.appContext.UMIS_CACHED_APIS[compiledUrl]) {
        return proxy.$.appContext.UMIS_CACHED_APIS[compiledUrl];
      }
    }
    const httpRequest = proxy.$api
      .slientApi(proxy.$umisConfig)
      [method](compiledUrl, fetchBody, cancelToken)
      .then((res) => {
        const result = res.data;
        if (!props.silentMessage) {
          proxy.$message({
            message: res.msg,
            showClose: true,
            type: 'success',
          });
        }
        return Promise.resolve(result);
      })
      .catch((err) => {
        if (err?.response?.data?.message && err.message !== 'CANCELED_DUE_TO_NEW_REQUEST') {
          proxy.$notice({
            type: 'error',
            title: '错误',
            customClass: 'umis-renderer__api-notice',
            dangerouslyUseHTMLString: true,
            message: `<div class="umis-singleline-ellipsis">路径：${err.response.config.url}</div><div>信息：${err.response.data.msg || err.response.data.message}</div>`,
          });
          return Promise.reject(err.response.data);
        } else {
          const message = err?.message || '错误';
          proxy.$message({
            message,
            showClose: true,
            type: 'error',
          });
          return Promise.reject(err.data);
        }
        if (err.message !== 'CANCELED_DUE_TO_NEW_REQUEST') {
          console.error(err);
        }
      });

    if (cached && !proxy.$.appContext.UMIS_CACHED_APIS[compiledUrl]) {
      proxy.$.appContext.UMIS_CACHED_APIS[compiledUrl] = httpRequest;
    }

    return httpRequest;
  };

  onBeforeUnmount(() => {
    if (proxy.syncLocation && props.initApi && props.initApi.params) {
      const defaultQuery = getUrlParams();
      for (const key in props.initApi.params) {
        if (Object.hasOwnProperty.call(props.initApi.params, key) && defaultQuery[key]) {
          delete defaultQuery[key];
        }
      }
      const newUrl = constructUrl(
        '',
        location.pathname,
        defaultQuery,
        location.hash,
        location.protocol,
      );
      window.history.replaceState({}, window.document.title, newUrl);
    }
  });

  return {
    handleFetchApi,
    shouldComponentUpdate,
  };
}
